<template>
    <div class="manage">
        <!--  -->
        <el-dialog title="" :visible.sync="dialogVisible" width="50%" :before-close="handleClose">
            <!--  -->
            <el-form ref="form" :rules="rules" :inline="true" :model="form" label-width="140px" >
                <el-form-item label="" prop="num">
                    <el-input placeholder="" v-model="form.num"></el-input>
                </el-form-item>
                <el-form-item label="" prop="name" >
                    <el-input placeholder="" v-model="form.name" ></el-input>
                </el-form-item>
                <el-form-item label="" prop="model">
                    <el-input placeholder="" v-model="form.model"></el-input>
                </el-form-item>
                <el-form-item label="" prop="facNum">
                    <el-input placeholder="" v-model="form.facNum"></el-input>
                </el-form-item>
                <el-form-item label="" prop="scheTime">
                    <el-date-picker v-model="form.scheTime" type="date" placeholder="" value-format="yyyy-MM-dd">
                    </el-date-picker>
                </el-form-item>
                <el-form-item label="" prop="cycle">
                    <el-input placeholder="" v-model="form.cycle"></el-input>
                </el-form-item>
                <el-form-item label="/" prop="verOrg">
                    <el-input placeholder="/" v-model="form.verOrg"></el-input>
                </el-form-item>
                <el-form-item label="" prop="type">
                    <el-select placeholder="" v-model="form.type" >
                        <el-option v-for="item in placeOptions" :key="item.value" :label="item.label" :value="item.value"></el-option>
                    </el-select>
                </el-form-item>
                <el-form-item label="" prop="remarks">
                    <el-input placeholder="" v-model="form.remarks"></el-input>
                </el-form-item>
            </el-form>
            <!--  -->
            <span slot="footer" class="dialog-footer">
                <el-button @click="cancel"> </el-button>
                <el-button type="primary" @click="submit"> </el-button>
            </span>
        </el-dialog>
        <!--  -->
        <el-form class="manage-header">
            <el-form-item style="width: 50vh;">
                <el-button @click="handleAdd" type="primary" style="margin-left:1vh;width: 13vh;font-size: 1.5vh;padding-left: 12%;"></el-button>
            </el-form-item>
            <el-form-item>
                <el-button @click="handleBatchDelete" type="danger" style="margin-left: 5%;width: 13vh;font-size: 1.5vh;padding-left: 18%;"></el-button>
            </el-form-item>
            <el-form-item>
                <!-- <el-upload   action="http://192.168.1.14:9090/equipInspPlan/import" :show-file-list="false" accept="xlsx"  -->
                <el-upload   :action="exportUrl"  :show-file-list="false" accept="xlsx" 
                      
                :on-success="handleExcelImportSuccess"
                :on-error="handleExcelImportFail"
                style="display: inline-block;">
                <el-button  style="margin-left: 3vh;width: 9vh;font-size: 1.5vh;padding-left: 21%;"  class = "ml-5" type="primary"><i class="el-icon-bottom"></i></el-button>
                </el-upload> </el-form-item>
            <el-form-item>
                <el-button @click="exp"  style="margin-left: 3vh;width: 9vh;font-size: 1.5vh;padding-left: 21%;" type="primary"><i class="el-icon-top">
                </i></el-button>
            </el-form-item>
            <el-form-item>
                <el-button @click="downloadTemplate" style="margin-left: 2vh;" class="download-template-btn" type="text"></el-button>
            </el-form-item>
            <el-form-item style="margin-left:45% ;">
                <el-input placeholder="" v-model="planForm.name" style="width: 20vh;font-size: 1.5vh; margin-left:-55%"></el-input>
            </el-form-item>
            <el-form-item>
                <el-button @click="load2" type="primary" style="margin-left: -8vh;width:7.5vh ;font-size: 1.5vh;"></el-button>
             </el-form-item>
            <el-form-item>
                <el-button type="warning" @click="reset" style="margin-left:3vh;width: 7.5vh;font-size: 1.5vh;padding-left: 21%;"></el-button>
            </el-form-item>
        </el-form>
        <el-tabs v-model="activeName" type="card" @tab-click="handleClick">
                <el-tab-pane label="" name="first"></el-tab-pane>
                <el-tab-pane label="EMC" name="second"></el-tab-pane>
                <el-tab-pane label="" name="third"></el-tab-pane>
                <el-tab-pane label="" name="fourth"></el-tab-pane>
        </el-tabs>
        <!--  -->
        <el-table  :data="tableData" style="width: 100%" border stripe
            @selection-change="handleSelectionChange">
            <el-table-column type="selection" width="55" align="center"></el-table-column>
            <el-table-column type="index" label="" width="60" align="center" :index="indexMethod"></el-table-column>
            <el-table-column prop="num" label="" align="center"></el-table-column>
            <el-table-column prop="name" label="" align="center"></el-table-column>
            <el-table-column prop="model" label="" align="center"></el-table-column>
            <el-table-column prop="facNum" label="" align="center"  show-overflow-tooltip></el-table-column>
            <el-table-column prop="scheTime" label="" align="center"></el-table-column>
            <el-table-column prop="cycle" label="" align="center"></el-table-column>
            <el-table-column prop="verOrg" label="/" align="center"  show-overflow-tooltip></el-table-column>
            <el-table-column prop="remarks" label="" align="center"  show-overflow-tooltip></el-table-column>
            <el-table-column label="" width="200" align="center">
                <template slot-scope="scope">
                    <el-button size="mini" type="primary" @click="handleEdit(scope.row)"></el-button>
                    <el-button style="margin-left:5px" size="mini" type="danger" @click="handleDelete(scope.row.id)">
                    </el-button>
                </template>
            </el-table-column>
        </el-table>
        <!--  -->
        <div style="padding: 10px 0">
            <el-pagination @size-change="handleSizeChange" @current-change="handleCurrentChange" :current-page="pageNum"
                :page-sizes="[2, 5, 10, 20]" :page-size="pageSize" layout="total, sizes, prev, pager, next, jumper"
                :total="total">
            </el-pagination>
        </div>
    </div>
</template>

<script>
export default {
    data() {
        return {
            dialogVisible: false,
            multipleSelection: [],
            activeName: 'first',
            tableData: [],
            modelType: 0,//01,
            total: 0,//
            pageNum: 1,
            pageSize: 10,
            form: {
                id: '',
                num: '',
                name: '',
                model: '',
                facNum: '',
                scheTime: '',
                cycle: '',
                verOrg: '',
                remarks: ''
            },
            planForm: {
                id: '',
                num: '',
                name: '',
                model: '',
                fac_num: '',
                sche_time: '',
                cycle: '',
                ver_org: '',
                remarks: ''
            },
            rules: {
                num: [
                    { required: true, message: "", trigger: "blur" }
                ],
                name: [
                    { required: true, message: "", trigger: "blur" }
                ],
                model: [
                    { required: true, message: "", trigger: "blur" }
                ],
                facNum: [
                    { required: true, message: "", trigger: "blur" }
                ],
                scheTime: [
                    { required: true, message: "", trigger: "blur" }
                ],
                cycle: [
                    { required: true, message: "", trigger: "blur" }
                ],
                ver_org: [
                    { required: true, message: "/", trigger: "blur" }
                ],
                 type: [
                    { required: true, message: "", trigger: "blur" }
                ]


            },
            placeOptions: [{
                value: 'first',
                label: ''
                }, {
                value: 'second',
                label: 'EMC'
                }, {
                value: 'third',
                label: ''
                },  {
                value: 'fourth',
                label: ''
                }]

        }
    },
    created() {
        this.load()
    },
    computed:{
        exportUrl(){
            return "http://192.168.1.14:9090/equipInspPlan/import?type=" + this.activeName
        },
    },
    methods: {
        handleClick() {
            console.log(this.activeName);
            this.pageNum=1
            this.load()

        },
        downloadTemplate(){
            // window.open("http://192.168.1.14:9090/file/d8ee16247c6a4d51b42a44ab5631db2e.xlsx")
            window.location.href = "http://192.168.1.14:9090/file/d8ee16247c6a4d51b42a44ab5631db2e.xlsx"

        },
        indexMethod(index) {
            let curpage = this.pageNum   //
            let limitpage = this.pageSize   //
            return (index + 1) + (curpage - 1) * limitpage
        },
        //
        submit() {
            this.$refs.form.validate((valid)=>{
            if(valid){
            this.request.post("/equipInspPlan", this.form).then(res => {
                if (res.code === "200") {
                    this.$message.success("")
                    this.dialogVisible = false
                    this.activeName = this.form.type
                    this.load()
                } else {
                    this.$message.error("")
                }
            })
            }else{
                    return false;                    
                }
             })
        },
        handleClose() {
            this.$refs.form.resetFields()
            this.dialogVisible = false
        },
        cancel() {
            this.handleClose()
        },
        //
        reset() {
        this.planForm.name = ""
        this.load()
        },
        //
        exp(){
            // window.open("http://192.168.1.14:9090/equipInspPlan/export")
            window.location.href="http://192.168.1.14:9090/equipInspPlan/export/"+ this.activeName

         },
         
         //
         handleExcelImportSuccess(){ 
            this.$message.success("")
            this.load()
         },
         //
        handleExcelImportFail(){
            this.$message.error("")
            this.load()
         },
        handleEdit(row) {
            this.modalType = 1
            // 
            this.form = JSON.parse(JSON.stringify(row))
            this.dialogVisible = true

        },
        handleDelete(id) {
            this.$confirm(', ?', '', {
                confirmButtonText: '',
                cancelButtonText: '',
                type: 'warning'
            }).then(() => {
                this.request.delete("/equipInspPlan/" + id).then(res => {
                    if (res.code === '200') {
                        this.$message({
                            type: 'success',
                            message: '!'
                        });
                    }
                    // 
                    this.load()
                })

            }).catch(() => {
                this.$message({
                    type: 'info',
                    message: ''
                });
            });
        },
        handleSelectionChange(val) {
            console.log(val)
            this.multipleSelection = val
        },
        handleBatchDelete() {
            if (this.multipleSelection.length === 0) {
                this.$message.warning("");
                return;
            }

            let ids = this.multipleSelection.map(v => v.id)  //[{},{}...]id[1.2.3]
            this.$confirm(', ?', '', {
                confirmButtonText: '',
                cancelButtonText: '',
                type: 'warning'
            }).then(() => {
                this.request.post("/equipInspPlan/del/batch", ids).then(res => {
                    if (res.code === '200') {
                        this.$message({
                            type: 'success',
                            message: '!'
                        });
                    }
                    // 
                    this.load()
                })

            }).catch(() => {
                this.$message({
                    type: 'info',
                    message: ''

                });
                this.load()
            });
        },
        handleAdd() {
            this.modelType = 0
            this.dialogVisible = true  //
            this.form = {}
        },
        load2(){
            this.pageNum = 1
            this.load()
        },
        load() {
            this.request.get("/equipInspPlan/page", {
                params: {
                    pageNum: this.pageNum,
                    pageSize: this.pageSize,
                    name: this.planForm.name,
                    type:this.activeName,
                }
            })
                .then(
                    res => {
                        console.log(res)
                        this.tableData = res.data.records
                        this.total = res.data.total
                    }
                )
        },
        handleSizeChange(pageSize) {
            console.log("pageSize:" + pageSize)
            this.pageSize = pageSize
            this.load()
        },
        handleCurrentChange(pageNum) {
            this.pageNum = pageNum
            this.load()
        },
        changeEnable(row) {
          
            this.request.post("/equipInspPlan", row).then(res => {
                if (res.data.code === '200') {
                    this.$message.success("")
                } else {
                    this.$message.error("")
                }
            })
        },
    },
};
</script>

<style lang="less" scoped>
.download-template-btn {
    border: none;
    background: none;
    // color: blue;
    text-decoration: underline;
    cursor: pointer;
