<template>
    <div class="manage">
        <!--  -->
        <el-dialog title="" :visible.sync="dialogVisible" width="50%" :before-close="handleClose">
            <!--  -->
            <el-form ref="form" :rules="rules" :inline="true" :model="form" label-width="80px">
                <el-form-item label="/" prop="selfNum">
                    <el-input placeholder="/" v-model="form.selfNum"></el-input>
                </el-form-item>
                <el-form-item label="/" prop="value">
                    <el-input placeholder="/" v-model="form.value"></el-input>
                </el-form-item>
                <el-form-item label="/" prop="position">
                    <el-input placeholder="/" v-model="form.position"></el-input>
                </el-form-item>
            </el-form>

            <!--  -->
            <span slot="footer" class="dialog-footer">
                <el-button @click="cancel"> </el-button>
                <el-button type="primary" @click="submit"> </el-button>
            </span>
        </el-dialog>
        <!--  -->
        <el-dialog title="" :visible.sync="updateDialogVisible" width="50%" :before-close="updateCancel">
            <!--  -->
            <el-form ref="form" :rules="rules" :inline="true" :model="form" label-width="80px">
          
               
                <el-form-item label="/" prop="selfNum">
                    <el-input placeholder="/" v-model="form.selfNum"></el-input>
                </el-form-item>
                <el-form-item label="/" prop="value">
                    <el-input placeholder="/" v-model="form.value"></el-input>
                </el-form-item>
                <el-form-item label="/" prop="position">
                    <el-input placeholder="/" v-model="form.position"></el-input>
                </el-form-item>
           
                
          
            </el-form>

            <!--  -->
            <span slot="footer" class="dialog-footer">
                <el-button @click="updateCancel"> </el-button>
                <el-button type="primary" @click="submitUpdate"> </el-button>
            </span>
        </el-dialog>
        <!--  -->
        <el-form :inline="true" :model="form" class="manage-header">
            <el-form-item>
                <el-button @click="handleBatchDelete" style="margin-left: 10px;" type="danger">
                    
                </el-button>
            </el-form-item>
            <el-form-item>
                <el-button @click="handleAdd" style="margin-left: 10px;" type="primary">
                    
                </el-button>
            </el-form-item>
            <!--form-->
            <el-form-item style="margin-left: 500px;">
                <el-input placeholder="" v-model="searchForm.value" suffix-icon="el-icon-search"></el-input>
            </el-form-item>
            <el-form-item>
                <el-button @click="load2" type="primary"></el-button>
                <el-button type="warning" @click="reset"></el-button>
            </el-form-item>
        </el-form>
        <!--  -->
        <!-- <el-tabs v-model="activeName" type="card" @tab-click="handleClick" style="margin-top: 10px; width: 90%;">
                <el-tab-pane label="" name="first"></el-tab-pane>
                <el-tab-pane label="EMC" name="second"></el-tab-pane>
                <el-tab-pane label="" name="third"></el-tab-pane>
                <el-tab-pane label="" name="fourth"></el-tab-pane>
          </el-tabs> -->
        <!--  -->
        <el-table  :data="tableData" style="width: 100%" border stripe
            @selection-change="handleSelectionChange">
            <el-table-column type="selection" width="125" align="center"></el-table-column>
            <el-table-column type="index" label="" width="250" align="center" :index="indexMethod"></el-table-column>
            <el-table-column prop="selfNum" label="/" width="250" align="center"></el-table-column>
            <el-table-column prop="value" label="/" width="250" align="center"></el-table-column>
            <el-table-column prop="position" label="/" width="250" align="center"></el-table-column>
            <!-- <el-table-column prop="enable" label="" align="center" width="80">
                <template slot-scope="scope">
                    <el-switch v-model="scope.row.enable" active-color="#13ce66" inactive-color="#ccc"
                        @change="changeEnable(scope.row)"></el-switch>
                </template>
            </el-table-column> -->
            <el-table-column label="" width="250" align="center">
                <template slot-scope="scope">
                    <el-button size="mini" type="primary" @click="handleEdit(scope.row)"></el-button>
                    <el-button style="margin-left:5px" size="mini" type="danger" @click="handleDelete(scope.row.id)">
                    </el-button>
                </template>
            </el-table-column>
        </el-table>
        <!--  -->
        <div style="padding: 10px 0">
            <el-pagination @size-change="handleSizeChange" @current-change="handleCurrentChange" :current-page="pageNum"
                :page-sizes="[2, 5, 10, 20]" :page-size="pageSize" layout="total, sizes, prev, pager, next, jumper"
                :total="total">
            </el-pagination>
        </div>
    </div>
</template>

<script>
export default {
    data() {
        return {
            dialogVisible: false,
            viewDialogVisible: false,
            updateDialogVisible:false,
            tableData: [],
            modelType: 0,//01,
            multipleSelection: [],
            type:"second",
            total: 0,//
            pageNum: 1,
            pageSize: 10,
            searchForm:{
                value: '',
            },
            form: {
                
            },
            rules: {
                value: [
                    { required: true, message: "", trigger: "blur" }
                ],
                position: [
                    { required: true, message: "", trigger: "blur" }
                ]
            },
        }
    },
    created() {
        this.load()
    },
    methods: {
        reset() {
            this.searchForm.value = ""
            this.load()
        },
        formatTimePeriod(timePeriod) {
            if (timePeriod === 1) {
                return '9:00-12:00';
            } if (timePeriod === 2) {
                return '13:00-17:00';
            }
        },
        indexMethod(index) {
            let curpage = this.pageNum   //
            let limitpage = this.pageSize   //
            return (index + 1) + (curpage - 1) * limitpage
        },
        handleDelete(id) {
            this.$confirm(', ?', '', {
                confirmButtonText: '',
                cancelButtonText: '',
                type: 'warning'
            }).then(() => {
                this.request.delete("/square/" + id).then(res => {
                    if (res.code === '200') {
                        this.$message({
                            type: 'success',
                            message: '!'
                        });
                    }
                    // 
                    this.load()
                })

            }).catch(() => {
                this.$message({
                    type: 'info',
                    message: ''
                });
            });
        },
        handleSelectionChange(val) {
            console.log(val)
            this.multipleSelection = val
        },
        handleBatchDelete() {
            if (this.multipleSelection.length === 0) {
                this.$message.warning("");
                return;
            }

            let ids = this.multipleSelection.map(v => v.id)  //[{},{}...]id[1.2.3]
            this.$confirm(', ?', '', {
                confirmButtonText: '',
                cancelButtonText: '',
                type: 'warning'
            }).then(() => {
                this.request.post("/square/del/batch", ids).then(res => {
                    if (res.code === '200') {
                        this.$message({
                            type: 'success',
                            message: '!'
                        });
                    }
                    // 
                    this.load()
                })

            }).catch(() => {
                this.$message({
                    type: 'info',
                    message: ''

                });
                this.load()
            });
        },
        load2(){
            this.pageNum = 1
            this.load()
        },
        load() {
            this.request.get("/square/page", {
                params: {
                    pageNum: this.pageNum,
                    pageSize: this.pageSize,
                    type: this.type,
                    value: this.searchForm.value

                }
            }).then(
                res => {
                    console.log(res)
                    this.tableData = res.data.records
                    this.total = res.data.total
                }
            )
        },
        handleCurrentChange(pageNum) {
            this.pageNum = pageNum
            this.load()
        },
        handleFileUploadSuccess() {
            this.$message.success("")
            this.load()
        },
        handleSizeChange(pageSize) {
            //   console.log(pageSize)
            this.pageSize = pageSize
            this.load()
        },
        handleAdd() {
            this.modelType = 0
            this.dialogVisible = true  //
            this.form = {}
        },

        handleEdit(row) {
            // this.form.role
            this.modalType = 1
            // 
            this.form = JSON.parse(JSON.stringify(row))
            // this.dialogVisible = true
            this.updateDialogVisible = true

        },

        updateCancel() {
            this.updateDialogVisible = false
        },
        cancel() {
            this.handleClose()
        },
        handleClose() {
            this.$refs.form.resetFields()
            this.dialogVisible = false
        },
             //
             submitUpdate() {
            this.$refs.form.validate((valid) => {
                if (valid) {
                    this.request.put("/square", this.form).then(res => {
                        if (res.code === "200") {
                            this.$message.success("")
                            this.updateDialogVisible = false
                            this.load()
                        } else {

                            this.$message.error("")
                        }
                    })
                } else {
                    return false;
                }
            })
        },
        submit() {
            this.$refs.form.validate((valid) => {
                if (valid) {
                    this.form.type = "second"
                    this.request.post("/square", this.form).then(res => {
                        if (res.code === "200") {
                            this.$message.success("")
                            this.dialogVisible = false
                            this.load()
                        } else {
                            this.$message.error("")
                        }
                    })
                } else {
                    return false;
                }
            })
        },
    },
};
</script>

<style lang="less" scoped>
.manage {
    height: 90%;

    .manage-header {
        display: flex;
        // justify-content: space-between;
        align-items: center;
    }

    .common-table {
  

        .pager {
            // position: absolute;
            // bottom: 0;
            // right: 20px;
            padding: 10px 0;
        }
    }
}
</style>