<script>
import HomeHeader from '../../../components/HomeHeader.vue';
import { validIdNum, validAge } from '../../../utils/validate';
export default {
  components: { HomeHeader },
  data() {
    const isIdNum = (rule, value, callback) => {
      validIdNum(value) ? callback() : callback(new Error(''))
    }
    const isAge = (rule, value, callback) => {
      validAge(value) ? callback() : callback(new Error(''))
    }
    return {
      form: {
        department: '',
        name: '',
        gender: '',
        birth: '',
        age: '',
        idNum: '',
        professionalComposition: '',
        eduBackground: '',
        major: '',
        gradInstitutions: '',
        gradTime: '',
        profQuality: '',
        profTitle: '',
        isInsured: '',
        enterTime: '',
        imgPath: '',
        professional: ''
      },
      rules: {
        department: [
          { required: true, message: '', trigger: 'blur' }
        ],
        name: [
          { required: true, message: '', trigger: 'blur' }
        ],
        jobNum: [
          { required: true, message: '', trigger: 'blur' }
        ],
        idNum: [
          { required: true, message: '', trigger: 'blur' },
          { validator: isIdNum, trigger: 'blur' }
        ],
        age: [
          { required: true, message: '', trigger: 'blur' },
          { validator: isAge, trigger: 'blur' }
        ]

      },
    }
  },
  created() {
    let user = localStorage.getItem("user") ? JSON.parse(localStorage.getItem("user")) : {}
    this.form.jobNum = user.id,
      this.form.name = user.loginName
    this.request.get("/userInfo/" + user.id).then(res => {
      console.log(res)
      if (res.code === '200') {
        this.form = res.data;
        this.form.jobNum = user.id,
          this.form.name = user.loginName
      }
    })
  },
  methods: {
    exit() {
      this.$router.push({ path: '/home' })
    },
    load() {
      let user = localStorage.getItem("user") ? JSON.parse(localStorage.getItem("user")) : {}

      this.request.get("/userInfo/" + user.id).then(res => {
        console.log(res)
        if (res.code === '200') {
          this.form = res.data;

        }
      })
    },
    open() {
      this.$refs.personalFormRef.validate((valid) => {
        if (valid) {
          this.$confirm(', ?', '', {
            confirmButtonText: '',
            cancelButtonText: '',
            type: 'warning'
          }).then(() => {
            this.request.post("/userInfo", this.form).then(res => {
              console.log(res)
              if (res.code == '200') {

                this.$message({
                  type: 'success',
                  message: '!'
                })
              } else {
                this.$message({
                  type: 'error',
                  message: ''
                })
              }
              this.load();
            })
          }).catch(() => {
            this.$message({
              type: 'info',
              message: ''
            });
            this.load();
          });
        } else {
          return false;
        }
      })
    },
    handleAvatarSuccess(res) {
      if (res) {
        console.log(res)
        this.form.imgPath = res;
      } else {
        console.error(' URL');
      }
    },
    handleAvatarFail() {
      this.$message({
        type: 'error',
        message: ''
      });
    }
  },
}
</script>

