<template>
    <div class="manage">
     
            <!--  -->
            <el-dialog title="" :visible.sync="dialogAduitVisible" width="50%" :before-close="handleClose">
            <!--  -->
            <el-form ref="form" :rules="rules" :inline="true" :model="form" label-width="80px">
                <el-table-column>
                <el-form-item label="" prop="status">
                    <el-radio v-model="form.status" label=""></el-radio>
                    <el-radio v-model="form.status" label=""></el-radio>
                </el-form-item>
                </el-table-column>

                <el-table-column>
                <el-form-item label="" prop="reason">

                    <el-input placeholder="" v-model="form.reason"></el-input>
                </el-form-item>
               </el-table-column>
                
            </el-form>
            <!--  -->
            <span slot="footer" class="dialog-footer">
                <el-button @click="cancel"> </el-button>
                <el-button type="primary" @click="submitAduit"> </el-button>
            </span>
        </el-dialog>
        
         <!--  -->
         <el-form :inline="true" :model="form" class="manage-header">
            <!--  -->
            <el-form-item>
                <el-button @click="handleBatchDelete" style="margin-left: 10px;" type="danger">
                    
                </el-button>
            </el-form-item>
            <!--form-->
            <el-form-item style="margin-left: 500px;">
                <el-input placeholder="" v-model="deviceForm.userName" suffix-icon="el-icon-search"></el-input>
            </el-form-item>
            <el-form-item>
                <el-button @click="load2" type="primary"></el-button>
                <el-button type="warning" @click="reset"></el-button>
            </el-form-item>
        </el-form>
    
      
        <!--  -->
        <el-table  :data="tableData" style="width: 100%" border stripe
            @selection-change="handleSelectionChange">
            <el-table-column type="selection" width="55" align="center"></el-table-column>
            <el-table-column type="index" label="" width="60" align="center" :index="indexMethod"></el-table-column>
            <el-table-column prop="userName" label=""  width="150" align="center"></el-table-column>
            <el-table-column prop="userId" label=""  width="100" align="center"></el-table-column>
            <el-table-column prop="customerName" label="" align="center"></el-table-column>
            <el-table-column prop="orderPlace" label=""  width="150" align="center"></el-table-column>
            <el-table-column prop="startTime" label=""  width="160" align="center"></el-table-column>
            <el-table-column prop="endTime" label=""  width="160" align="center"></el-table-column>

            <!-- <el-table-column prop="timePeriod" label="" width="150" align="center">
                <template slot-scope="scope">
                    {{ formatTimePeriod(scope.row.timePeriod) }}
                </template>
            </el-table-column> -->
            <el-table-column prop="submitTime" label="" type="datetime" width="170" align="center"></el-table-column>
            <!-- <el-table-column prop="enabled" label="" align="center" width="80">
                <template slot-scope="scope">
                    <el-switch v-model="scope.row.enabled" active-color="#13ce66" inactive-color="#ccc"
                        @change="changeEnable(scope.row)"></el-switch>
                </template>
            </el-table-column> -->
            <el-table-column prop="status" label="" width="120" align="center">
                <template slot-scope="scope">
                    <el-tag type="success" v-if="scope.row.status === ''"></el-tag>
                    <el-tag type="warning" v-if="scope.row.status === ''"></el-tag>
                    <el-tag type="danger" v-if="scope.row.status === ''"></el-tag>
                </template>
            </el-table-column>
            <el-table-column prop="reason" label="" width="170" align="center" show-overflow-tooltip=""></el-table-column>

            <el-table-column label="" width="200" align="center">
                <template slot-scope="scope">
                    <!-- :disabled="scope.row.status === ''" -->
                    <el-button style="margin-left:5px" size="mini" type="success"  @click="handleAudit(scope.row)">
                    </el-button>
                    <el-button style="margin-left:5px" size="mini" type="danger" @click="handleDelete(scope.row.id)">
                    </el-button>
                </template>
            </el-table-column>
        </el-table>

        <!--  -->
        <div style="padding: 10px 0">
            <el-pagination @size-change="handleSizeChange" @current-change="handleCurrentChange" :current-page="pageNum"
                :page-sizes="[2, 5, 10, 20]" :page-size="pageSize" layout="total, sizes, prev, pager, next, jumper"
                :total="total">
            </el-pagination>
        </div>
    </div>
</template>

<script>
export default {
    data() {
        return {
            dialogAduitVisible: false,
            tableData: [],
            modelType: 0,//01,
            multipleSelection: [],
            deviceForm:{},
            total: 0,//
            pageNum: 1,
            pageSize: 10,
            type: "second",
            form: {
                status:'',
                reason:'',
                // userName: '',
            },
            rules: {
        //
            status: [
                        { required: true, message: "", trigger: "blur" }
                    ],
            },
        }
    },
    created() {
        this.load()
    },
    methods: {
        submitAduit() {
            this.$refs.form.validate((valid) => {
                if (valid) {
                    //TODO:
                    this.request.post("/equipmentOrder/update", this.form).then(res => {
                        if (res.code === "200") {
                            this.$message.success("")
                            this.dialogAduitVisible = false
                            this.load()
                        } else if(res.code === "402"){
                            this.$message.error(res.msg)
                        }
                        else {
                            this.$message.error("")
                        }
                    })
                } else {
                    return false;
                }
            })
        },
        handleClose() {
            this.$refs.form.resetFields()
            this.dialogAduitVisible = false
            this.load()
        },
        cancel() {
            this.handleClose()
        },
        handleAudit(row){
            this.form= row
            this.dialogAduitVisible = true
            
        },
     
        indexMethod(index) {
            let curpage = this.pageNum   //
            let limitpage = this.pageSize   //
            return (index + 1) + (curpage - 1) * limitpage
        },
        handleDelete(id) {
            this.$confirm(', ?', '', {
                confirmButtonText: '',
                cancelButtonText: '',
                type: 'warning'
            }).then(() => {
                this.request.delete("/equipmentOrder/back/" + id).then(res => {
                    if (res.code === '200') {
                        this.$message({
                            type: 'success',
                            message: '!'
                        });
                    }
                    // 
                    this.load()
                })

            }).catch(() => {
                this.$message({
                    type: 'info',
                    message: ''
                });
            });
        },
        handleSelectionChange(val) {
            console.log(val)
            this.multipleSelection = val
        },
        handleBatchDelete() {
            if (this.multipleSelection.length === 0) {
                this.$message.warning("");
                return;
            }

            let ids = this.multipleSelection.map(v => v.id)  //[{},{}...]id[1.2.3]
            this.$confirm(', ?', '', {
                confirmButtonText: '',
                cancelButtonText: '',
                type: 'warning'
            }).then(() => {
                this.request.post("/equipmentOrder/del/batch", ids).then(res => {
                    if (res.code === '200') {
                        this.$message({
                            type: 'success',
                            message: '!'
                        });
                    }
                    // 
                    this.pageNum = 1
                    this.load()
                })

            }).catch(() => {
                this.$message({
                    type: 'info',
                    message: ''

                });
                this.load()
            });
        },
        // changeEnable(row) {
        //     // console.log("1111")
        //     // this.request.post("/equipmentOrder", row).then(res => {
        //     this.request.post("/equipmentOrder/update", row).then(res => {

        //         if (res.code === '200') {
        //             this.$message.success("")
        //         } else if(res.code === '402'){  // 
        //             this.$message.warning("")
        //             row.enabled = false
        //         }
        //         else {
        //             this.$message.error("")
        //         }
        //     })
        // },
        load2(){
            this.pageNum = 1
            this.load()
        },
        load() {
            this.request.get("/equipmentOrder/page" , {
                params: {
                pageNum: this.pageNum,
                pageSize: this.pageSize,
                type: this.type,
                userName:this.deviceForm.userName
                }
            }).then(
                    res => {
                        console.log(res)
                        this.tableData = res.data.records
                        this.total = res.data.total
                    }
                )
        },
        handleCurrentChange(pageNum) {
            this.pageNum = pageNum
            this.load()
        },
        reset() {
            this.deviceForm.userName = ""
            this.load()
        },
        handleFileUploadSuccess() {
            this.$message.success("")
            this.load()
        },
        handleSizeChange(pageSize) {
            //   console.log(pageSize)
            this.pageSize = pageSize
            this.load()
        }
    },
};
</script>

<style lang="less" scoped>
.manage {
    height: 90%;

    .manage-header {
        display: flex;
        // justify-content: space-between;
        align-items: center;
    }

    .common-table {
        position: relative;
        height: calc(100% - 62px);

        .pager {
            // position: absolute;
            // bottom: 0;
            // right: 20px;
            padding: 10px 0;
        }
    }
}
</style>