<template>
  <el-dialog :visible.sync="viewDialogVisible" width="50%" :show-close="false">
    <div slot="title" class="dialog-title">
      <h1></h1>
    </div>
    <!--  -->
    <div class="center-content">
      <el-form :model="userDetails" label-width="0.6rem" font-size="0.4rem">
        <el-row >
          <el-col :span="12" class="vertical-line">
            <el-form-item label="" style="font-size: 0.5rem;"><span style="font-size: 0.05rem;">{{ userDetails.department }}</span></el-form-item>
          </el-col>
          <el-col :span="12">
            <el-form-item label="">{{ userDetails.joinDate }}</el-form-item>
          </el-col>
        </el-row>
        <el-row>
          <el-col :span="12" class="vertical-line">
            <el-form-item label="">{{ userDetails.name }}</el-form-item>
          </el-col>
          <el-col :span="12">
            <el-form-item label="">{{ userDetails.employeeId }}</el-form-item>
          </el-col>
        </el-row>
        <el-row>
          <el-col :span="12" class="vertical-line">
            <el-form-item label="">{{ userDetails.gender }}</el-form-item>
          </el-col>
          <el-col :span="12">
            <el-form-item label="">{{ userDetails.birthdate }}</el-form-item>
          </el-col>
        </el-row>
        <el-row>
          <el-col :span="12" class="vertical-line">
            <el-form-item label="">{{ userDetails.age }}</el-form-item>
          </el-col>
          <el-col :span="12">
            <el-form-item label=""><span style="font-size: 0.05rem;">{{ userDetails.idCard }}</span></el-form-item>
          </el-col>
        </el-row>
        <el-row>
          <el-col :span="12" class="vertical-line">
            <el-form-item label="">{{ userDetails.majorComposition }}</el-form-item>
          </el-col>
          <el-col :span="12">
            <el-form-item label="">{{ userDetails.educationLevel }}</el-form-item>
          </el-col>
        </el-row>
        <el-row>
          <el-col :span="12" class="vertical-line">
            <el-form-item label="">{{ userDetails.major }}</el-form-item>
          </el-col>
          <el-col :span="12">
            <el-form-item label="">{{ userDetails.graduateSchool }}</el-form-item>
          </el-col>
        </el-row>
        <el-row>
          <el-col :span="12" class="vertical-line">
            <el-form-item label="">{{ userDetails.titleName }}</el-form-item>
          </el-col>
          <el-col :span="12">
            <el-form-item label="">{{ userDetails.titleLevel }}</el-form-item>
          </el-col>
        </el-row>
        <el-row>
          <el-col :span="12" class="vertical-line">
            <el-form-item label="">{{ userDetails.graduateDate }}</el-form-item>
          </el-col>
          <el-col :span="12">
            <el-form-item label="">{{ userDetails.insured ? '' : '' }}</el-form-item>
          </el-col>
        </el-row>
        <el-row>
          <el-col :span="12">
            <el-form-item label="">{{ userDetails.professional ? '' : '' }}</el-form-item>
          </el-col>
        </el-row>
      </el-form>
      <!--  -->
      <span slot="footer" class="dialog-footer" >
        <el-button @click="closeDetailsDialog" class="close"></el-button>
      </span>
    </div>
  </el-dialog>
</template>

<script>
export default {
  props: {
    user: Object, // 
  },
  data() {
    return {
      viewDialogVisible: false, // 
      userDetails: {
        department: '',
        joinDate: '',
        name: '',
        employeeId: '',
        gender: '',
        birthdate: '',
        age: '',
        idCard: '',
        majorComposition: '',
        educationLevel: '',
        major: '',
        graduateSchool: '',
        titleName: '',
        titleLevel: '',
        graduateDate: '',
        insured: false,
      },
    };
  },
  watch: {
    user: {
      handler(newUser) {
        this.userDetails = {
          department: newUser.department,
          joinDate: newUser.enterTime,
          name: newUser.name,
          employeeId: newUser.jobNum,
          gender: newUser.gender,
          birthdate: newUser.birth,
          age: newUser.age,
          idCard: newUser.idNum,
          majorComposition: newUser.profTitle,
          educationLevel: newUser.eduBackground,
          major: newUser.major,
          graduateSchool: newUser.gradInstitutions,
          titleName: newUser.professionalComposition,
          titleLevel: newUser.profQuality,
          graduateDate: newUser.gradTime,
          insured: newUser.isInsured,
        };
        this.viewDialogVisible = true; // 
      },
      immediate: true,
    },
  },
  methods: {
    closeDetailsDialog() {
      this.viewDialogVisible = false; // 
    },
  },
};
</script>

<style scoped>
/*  */
.center-text {
  text-align: center;
  font-size: 0.13rem;
}

.dialog-title {
  text-align: center;
  font-size: 0.13rem;
  padding: 20px 0; 

}

.center-content {
  text-align: center;
  width: 70%; /*  */
  margin-left: 16%; /*  */
  margin-top: -5%;
  font-size: 0.2rem;
}

.el-row {
  border: 1px solid #e0e0e0;
  padding-right: 0px 0;
  display: flex; /*  Flex  */
  align-items: center; /*  */
  height: 0.26rem;
font-size: 0.5rem;
}

.vertical-line {
  border-right: 1px solid #e0e0e0; /*  */
font-size: 0.2rem;
}

.el-form-item {
  margin-bottom: 0px;
  background: linear-gradient(to right, #f5f5f5 40%, transparent 20%)
}

/*  center-text  */

body.el-form-item__label {
  background-color: #ccc;  /*  */
  padding: 0px;  /*  */
  display: inline-block;  /* label */
}
.close{
background-color:rgb(55, 115, 176);
color: #faf9f9; /*  */
font-weight: bold; /*  */
margin-top: 0.3rem;
font-size: 0.07rem;
}
.close:hover{
background-color:#409EFF;
color: #faf9f9; /*  */
font-weight: bold; /*  */
margin-top:0.3rem;
font-size: 0.07rem;
}
.alternate-row {
  background-color: #f5f5f5;
  font-size: 0.5rem;
}
</style>

