<template>
    <div class="manage">
        <!--  -->
        <el-dialog title="" :visible.sync="dialogVisible" width="50%" :before-close="handleClose">
            <!--  -->
            <el-form ref="form" :rules="rules" :inline="true" :model="form" label-width="80px">
                <el-form-item label="" prop="examineName">
                    <el-input placeholder="" v-model="form.examineName"></el-input>
                </el-form-item>
                <el-form-item label="" prop="model">
                    <el-input placeholder="" v-model="form.model"></el-input>
                </el-form-item>
                <el-form-item label="" prop="number">
                    <el-input placeholder="" v-model="form.number"></el-input>
                </el-form-item>

                <!-- <el-form-item label="" prop="model">
                    <el-select placeholder="" v-model="form.model" @change="updateNumberOptions">
                        <el-option v-for="(item, index) in modelOptions" :key="index" :label="item" :value="item"></el-option>
                    </el-select>
                </el-form-item>

                <el-form-item label="" prop="number">
                    <el-select placeholder="" v-model="form.number" disabled>
                        <el-option v-for="(item, index) in numberOptions" :key="index" :label="item" :value="item"></el-option>
                    </el-select>
                </el-form-item> -->

                <el-form-item label="" prop="examineTime">
                    <el-date-picker v-model="form.examineTime" type="date" placeholder="" value-format="yyyy-MM-dd">
                    </el-date-picker>
                </el-form-item>
                <!-- <el-form-item  prop="type">
                    <el-input v-model="form.type" v-show="false"></el-input>
                    
                </el-form-item> -->

                <el-form-item label="" prop="type">
                    <el-select placeholder="" v-model="form.type">
                        <el-option v-for="item in placeOptions" :key="item.value" :label="item.label"
                            :value="item.value"></el-option>
                    </el-select>
                </el-form-item>
                <el-form-item label="" prop="remarks">
                    <el-input placeholder="" v-model="form.remarks"></el-input>
                </el-form-item>
            </el-form>
            <!--  -->
            <span slot="footer" class="dialog-footer">
                <el-button @click="cancel"> </el-button>
                <el-button type="primary" @click="submit"> </el-button>
            </span>
        </el-dialog>

        <!--  -->
        <el-form class="manage-header">
            <!--  -->
            <el-form-item>
                <el-button @click="handleAdd" type="primary"
                    style="margin-left:1vh;width: 13vh;font-size: 1.5vh;padding-left: 12%;"></el-button>
            </el-form-item>
            <el-form-item>
                <el-button @click="handleBatchDelete" type="danger"
                    style="margin-left: 5%;width: 13vh;font-size: 1.5vh;padding-left: 18%;"></el-button>
            </el-form-item>
            <el-form-item>
                <!-- <el-upload   action="'http://192.168.1.14:9090/examinePlan/import?type=' + 'this.activeName'"  :show-file-list="false" accept="xlsx"  -->
                <el-upload :action="exportUrl" :show-file-list="false" accept="xlsx" :on-success="handleExcelImportSuccess"
                    :on-error="handleExcelImportFail" style="display: inline-block;">
                    <el-button style="margin-left: 3vh;width: 9vh;font-size: 1.5vh;padding-left: 21%;" class="ml-5"
                        type="primary"><i class="el-icon-bottom"></i></el-button>
                </el-upload> </el-form-item>
            <el-form-item>
                <el-button @click="exp" style="margin-left: 3vh;width: 9vh;font-size: 1.5vh; padding-left: 21%;"
                    type="primary"><i class="el-icon-top">
                    </i></el-button>
            </el-form-item>
            <el-form-item>
                <el-button @click="downloadTemplate" style="margin-left: 2vh;" class="download-template-btn"
                    type="text"></el-button>
            </el-form-item>
            <el-form-item style="margin-left: 45%">
                <el-input placeholder="" v-model="verPlanForm.examineName"
                    style="width: 20vh;font-size: 1.5vh;margin-left:-55%"></el-input>
            </el-form-item>
            <el-form-item>
                <el-button @click="load2" type="primary"
                    style="margin-left: -8vh;width:7.5vh ;font-size: 1.5vh;"></el-button> </el-form-item>
            <el-form-item>
                <el-button type="warning" @click="reset"
                    style="margin-left: 3vh;width:7.5vh ;font-size: 1.5vh;padding-left: 21%;"></el-button>
            </el-form-item>

        </el-form>
        <el-form>
            <el-form-item style="color: red; font-weight: bold; margin-left: 5vh; white-space: nowrap; width: 100%;">{{
                this.dayCount }} 30
            </el-form-item>
        </el-form>
        <!--  -->
        <el-tabs v-model="activeName" type="card" @tab-click="handleClick">
            <el-tab-pane label="" name="first"></el-tab-pane>
            <el-tab-pane label="EMC" name="second"></el-tab-pane>
            <el-tab-pane label="" name="third"></el-tab-pane>
            <el-tab-pane label="" name="fourth"></el-tab-pane>
        </el-tabs>
        <!--  -->
        <el-table height="3rem" :data="tableData" style="width: 100%" border @selection-change="handleSelectionChange"
            :row-class-name="tableRowClassName">
            <el-table-column type="selection" width="55" align="center"></el-table-column>
            <el-table-column type="index" label="" width="60" align="center" :index="indexMethod"></el-table-column>
            <el-table-column prop="examineName" label="" align="center"></el-table-column>
            <el-table-column prop="model" label="" align="center"></el-table-column>
            <el-table-column prop="number" label="" align="center"></el-table-column>
            <el-table-column prop="examineTime" label="" align="center"></el-table-column>
            <el-table-column prop="remarks" label="" align="center"></el-table-column>
            <el-table-column label="" width="200" align="center">
                <template slot-scope="scope">
                    <el-button size="mini" type="primary" @click="handleEdit(scope.row)"></el-button>
                    <el-popconfirm class="ml-5" confirm-button-text="" cancel-button-text="" ican="el-icon-info"
                        icon-color="red" title="" @confirm="del(scope.row.id)">
                    </el-popconfirm>
                    <el-button style="margin-left:5px" size="mini" type="danger" @click="handleDelete(scope.row.id)">
                    </el-button>
                </template>
            </el-table-column>
        </el-table>
        <!--  -->
        <div style="padding: 10px 0">
            <el-pagination @size-change="handleSizeChange" @current-change="handleCurrentChange" :current-page="pageNum"
                :page-sizes="[2, 5, 10, 20]" :page-size="pageSize" layout="total, sizes, prev, pager, next, jumper"
                :total="total">
            </el-pagination>
        </div>
    </div>
</template>

<script>
import request from '@/utils/request';
export default {
    data() {
        return {
            dialogVisible: false,
            tableData: [],
            multipleSelection: [],
            activeName: 'first',

            modelType: 0,//01,
            total: 0,//
            pageNum: 1,
            pageSize: 10,
            dayCount: 0,
            form: {
                examineName: '',
                model: '',
                number: '',
                examineTime: '',
                type: '',
                remarks: ''
            },
            verPlanForm: {
                examineName: '',

            },
            rules: {
                examineName: [
                    { required: true, message: "", trigger: "blur" }
                ],
                model: [
                    { required: true, message: "", trigger: "blur" }
                ],
                number: [
                    { required: true, message: "", trigger: "blur" }
                ],
                examineTime: [
                    { required: true, message: "", trigger: "blur" }
                ],
                type: [
                    { required: true, message: "", trigger: "blur" }
                ]


            },

            //TODO
            modelOptions: ['ESCI', 'FSV 30', 'VULB9163', 'BBHA 9120D', 'FPA-6592G', '8449B',
                'SG6000', '150W1000', 'BLMA1060-200', 'STLP 9128D', 'FL7006', 'PM2002'],
            numberOptions: ['100983', '101374', '1178', '920', '060020', '3008A02604',
                '0327336', '0332731', '1711332', '17', '0332270', '0328313'],
            placeOptions: [{
                value: 'first',
                label: ''
            }, {
                value: 'second',
                label: 'EMC'
            }, {
                value: 'third',
                label: ''
            }, {
                value: 'fourth',
                label: ''
            }]

            // rules:{

            // }

        }
    },
    created() {
        this.load()
        this.getDayCount()
    },
    computed: {
        exportUrl() {
            // 
            // console.log( 11)
            // console.log(  this.request.defaults.baseURL)
            // return  this.request.defaults.baseURL+"/examinePlan/import?type="+ this.activeName
            return "http://192.168.1.14:9090/examinePlan/import?type=" + this.activeName
        },
    },
    methods: {
        getDayCount() {
            this.request.get("/examinePlan/expire").then(res => {
                // console.log("hideField")
                // console.log(res)
                this.dayCount = res.data;
            });
        },

        // exportUrl: ,
        handleClick() {
            console.log(this.activeName);
            this.pageNum = 1
            this.load()

        },
        downloadTemplate() {
            // window.open("http://192.168.1.14:9090/file/48a507aab0ac4e11a604f1002034b3be.xlsx")
            window.location.href = "http://192.168.1.14:9090/file/48a507aab0ac4e11a604f1002034b3be.xlsx"
        },
        indexMethod(index) {
            let curpage = this.pageNum   //
            let limitpage = this.pageSize   //
            return (index + 1) + (curpage - 1) * limitpage
        },
        load2() {
            this.pageNum = 1
            this.load()
        },
        load() {
            this.request.get("/examinePlan/page", {
                params: {
                    pageNum: this.pageNum,
                    pageSize: this.pageSize,
                    name: this.verPlanForm.examineName,
                    type: this.activeName
                }
            }).then(res => {
                this.tableData = res.data.records
                this.total = res.data.total
            }
            )
        },
        //
        reset() {
            this.verPlanForm.examineName = ""
            this.load()
        },
        //
        updateNumberOptions() {
            const model = this.form.model;
            const index = this.modelOptions.indexOf(model);
            if (index !== -1) {
                this.form.number = this.numberOptions[index];
            } else {
                this.form.number = '';
            }
        },
        //
        exp() {
            // window.open("http://192.168.1.14:9090/examinePlan/export")
            window.location.href = "http://192.168.1.14:9090/examinePlan/export/" + this.activeName
        },

        //
        handleExcelImportSuccess() {
            this.$message.success("")
            this.load()
        },
        //
        handleExcelImportFail() {
            this.$message.error("")
            this.load()
        },
        //
        handleAdd() {
            this.modelType = 0
            this.dialogVisible = true  //
            this.form = {}
        },
        //
        handleClose() {
            this.$refs.form.resetFields()
            this.dialogVisible = false
        },
        cancel() {
            this.handleClose()
        },
        //
        submit() {
            this.$refs.form.validate((valid) => {
                if (valid) {
                    this.request.post("/examinePlan", this.form).then(res => {
                        if (res.code === "200") {
                            this.$message.success("")
                            this.dialogVisible = false
                            this.activeName = this.form.type
                            this.load()
                        } else {
                            this.$message.error("")
                        }
                    })
                } else {
                    return false;
                }
            })
        },
        handleEdit(row) {
            this.modalType = 1
            // 
            this.form = JSON.parse(JSON.stringify(row))
            this.dialogVisible = true

        },
        handleDelete(id) {
            this.$confirm(', ?', '', {
                confirmButtonText: '',
                cancelButtonText: '',
                type: 'warning'
            }).then(() => {
                this.request.delete("/examinePlan/" + id).then(res => {
                    if (res.code === '200') {
                        this.$message({
                            type: 'success',
                            message: '!'
                        });
                    }
                    // 
                    this.load()
                })

            }).catch(() => {
                this.$message({
                    type: 'info',
                    message: ''
                });
            });
        },
        handleSelectionChange(val) {
            console.log(val)
            this.multipleSelection = val
        },
        handleBatchDelete() {
            if (this.multipleSelection.length === 0) {
                this.$message.warning("");
                return;
            }

            let ids = this.multipleSelection.map(v => v.id)  //[{},{}...]id[1.2.3]
            this.$confirm(', ?', '', {
                confirmButtonText: '',
                cancelButtonText: '',
                type: 'warning'
            }).then(() => {
                this.request.post("/examinePlan/del/batch", ids).then(res => {
                    if (res.code === '200') {
                        this.$message({
                            type: 'success',
                            message: '!'
                        });
                    }
                    // 
                    this.load()
                })

            }).catch(() => {
                this.$message({
                    type: 'info',
                    message: ''

                });
                this.load()
            });
        },

        handleCurrentChange(pageNum) {
            this.pageNum = pageNum
            this.load()
        },
        handleFileUploadSuccess() {
            this.$message.success("")
            this.load()
        },
        handleSizeChange(pageSize) {
            //   console.log(pageSize)
            this.pageSize = pageSize
            this.load()
        },
        changeEnable(row) {
            // this.axios.post("http://192.168.1.14:9090/examinePlan", row).then(res => {
            this.request.post("/examinePlan", row).then(res => {
                if (res.data.code === '200') {
                    this.$message.success("")
                } else {
                    this.$message.error("")
                }
            })
        },
        tableRowClassName({ row, rowIndex }) {
            const today = new Date();
            const validityDate = new Date(row.examineTime);
            console.log("validityDate", validityDate);

            // 
            const daysRemaining = Math.floor((validityDate - today) / (1000 * 60 * 60 * 24) + 1);


            // 30
            if (0 <= daysRemaining && daysRemaining <= 30) {

                // return this.selectedColor; // 
                return { 'highlight-row': true, 'top': '0' }; // 
            }

            if (daysRemaining < 0) {
                return { 'gray-row': true, 'bottom': '0' }; // 
            }

            return '';// 
        },
    },
};
</script>

