<script>
import HomeHeader from "../../../components/HomeHeader.vue";
import HomeFooter from "../../../components/HomeFooter.vue";
export default {
  components: { HomeHeader, HomeFooter },
  data() {
    return {
      dialogVisible: false,
      multipleSelection: [],
      activeName: 'first',
      tableData: [],
      validityFilters: [],
      verOrgFilters: [],
      cycleFilters: [],
      filtersForm: {
        validity: [],
        verOrg: [],
        cycle: []
      },
      filteredData: [], // 
      modelType: 0,//01,
      total: 0,//
      pageNum: 1,
      pageSize: 10,
      equipForm: {
        id: '',
        selfNum: '',
        equipName: '',
        model: '',
        facNum: '',
        cerNum: '',
        validity: '',
        cycle: '',
        verOrg: '',
        remarks: '',
        measureRange: '',
        accuracy: '',
        usePlace: '',
        manuFact: ''
      },
      form: {
        id: '',
        selfNum: '',
        equipName: '',
        model: '',
        facNum: '',
        cerNum: '',
        validity: '',
        cycle: '',
        verOrg: '',
        remarks: '',
        measureRange: '',
        accuracy: '',
        usePlace: '',
        manuFact: ''
      },
      columns: [
        //  {label:"", prop:"selfNum" ,isShow:true},
        //  {label:"",prop:"equipName",isShow:true},
        //  {label:"",prop:"model",isShow:true},


      ],
      rules: {
        selfNum: [
          { required: true, message: "", trigger: "blur" }
        ],
        equipName: [
          { required: true, message: "", trigger: "blur" }
        ],
        model: [
          { required: true, message: "", trigger: "blur" }
        ],
        facNum: [
          { required: true, message: "", trigger: "blur" }
        ],
        cerNum: [
          { required: true, message: "", trigger: "blur" }
        ],
        validity: [
          { required: true, message: "", trigger: "blur" }
        ],
        cycle: [
          { required: true, message: "", trigger: "blur" }
        ],
        verOrg: [
          { required: true, message: "/", trigger: "blur" }
        ],
        measureRange: [
          { required: true, message: "", trigger: "blur" }
        ],
        accuracy: [
          { required: true, message: "", trigger: "blur" }
        ],
        usePlace: [
          { required: true, message: "", trigger: "blur" }
        ],
        manuFact: [
          { required: true, message: "", trigger: "blur" }
        ]
      }
    }
  },
  created() {
    this.load()
    this.getAll()
    this.getAllField()
  },
  methods: {
    //
    getAllField() {
      this.request.get("/hideField").then(res => {

        this.columns = res.data;
      });
    },
    // 
    showColumn(currentColumn) {
      return this.columns.find(item => item.prop == currentColumn).isShow;
    },
    handleClick() {
      // console.log(this.activeName);
      this.pageNum = 1
      this.load()

    },
    load2() {
      this.pageNum = 1
      this.load()
    },
    load() {
      this.request.get("/equipLedger/front/page", {
        params: {
          pageNum: this.pageNum,
          pageSize: this.pageSize,
          name: this.equipForm.name,
          model: this.equipForm.model,
          type: this.activeName,

        }
      }).then(res => {
        this.tableData = res.data.records
        this.total = res.data.total
        //
        this.getAll()

      }
      )
    },
    getAll() {
      this.request.get("/equipLedger").then(res => {
        const records = res.data;
        this.handleFilterField(records)

      });
    },
    reset() {
      this.equipForm.name = ""
      this.equipForm.model = ""
      this.activeName = "first"
      this.filtersForm.validity = ""
      this.filtersForm.verOrg = ""
      this.filtersForm.cycle = ""
      // this.$refs.filterTable.clearFilter()
      this.load()
    },
    handleFilterField(records) {
      // 
      // 
      const dates = records.map(record => record.validity);

      //  Set 
      const uniqueDates = [...new Set(dates)];
      // 
      uniqueDates.sort((a, b) => new Date(a) - new Date(b));
      //  this.tableData  this.validityFilters
      // this.tableData = records;
      this.validityFilters = uniqueDates.map(date => {
        return {
          text: date,
          value: date
        };
      });



      ///
      const verOrgs = records.map(record => record.verOrg)
      const uniqueVerOrgs = [...new Set(verOrgs)];
      this.verOrgFilters = uniqueVerOrgs.map(verOrg => {
        return {
          text: verOrg,
          value: verOrg
        };
      });
      //
      const cycles = records.map(record => record.cycle)
      const uniqueCycles = [...new Set(cycles)];
      this.cycleFilters = uniqueCycles.map(cycle => {
        return {
          text: cycle,
          value: cycle
        };
      });
    },
    handeleFilterChange(filter) {
      //
      this.pageNum = 1
      //
      if (filter.validity)
        this.filtersForm.validity = filter.validity.join(',')
      if (filter.cycle)
        this.filtersForm.cycle = filter.cycle.join(',')
      if (filter.verOrg)
        this.filtersForm.verOrg = filter.verOrg.join(',')

      this.request.get("/equipLedger/filter", {
        params: {
          pageNum: this.pageNum,
          pageSize: this.pageSize,
          validity: this.filtersForm.validity,
          verOrg: this.filtersForm.verOrg,
          cycle: this.filtersForm.cycle
        }

      }).then(res => {
        this.tableData = res.data.records
        this.total = res.data.total
        this.handleFilterField(res.data.records)
      });
    },
    cancel() {
      this.dialogVisible = false;
    },
    handleSelectionChange(val) {
      console.log(val);
      this.multipleSelection = val;
    },
    handleCurrentChange(pageNum) {
      this.pageNum = pageNum;
      this.load();
    },
    handleSizeChange(pageSize) {
      this.pageSize = pageSize;
      this.load();
    },
    handleDelete(id) {
      this.$confirm("", "", {
        confirmButtonText: "",
        cancelButtonText: "",
        type: "warning",
      })
        .then(() => {
          this.request.delete("/order/" + id).then((res) => {
            if (res.code === "200") {
              this.$message({
                type: "success",
                message: "!",
              });
            } else {
              this.$message({
                type: "error",
                message: "!",
              });
            }

            // 
            this.load();
          });
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: "",
          });
        });
    },
    deviceinspplan() {
      this.$router.push({ path: '/devicehome/deviceinspplan' })
    },
    FixedAssets() {
      this.$router.push({ path: '/devicehome/FixedAssets' })
    },
    periodverificationplanf() {
      this.$router.push({ path: '/devicehome/periodverificationplanf' })
    },

    tableRowClassName({ row, rowIndex }) {
      if (rowIndex === 1) {
        return 'warning-row';
      } else if (rowIndex === 3) {
        return 'success-row';
      }
      return '';
    },
    indexMethod(index) {
      let curpage = this.pageNum   //
      let limitpage = this.pageSize   //
      return (index + 1) + (curpage - 1) * limitpage
    },
    tableRowClassName({ row, rowIndex }) {
      const today = new Date();
      const validityDate = new Date(row.validity);
      // console.log("today" + today)
      // console.log("validityDate" + validityDate)
      validityDate.setMonth(validityDate.getMonth() + row.cycle);
      // 
      const daysRemaining = Math.floor((validityDate - today) / (1000 * 60 * 60 * 24) + 1);
      console.log("daysRemaining" + daysRemaining)

      // 30
      if (0 <= daysRemaining && daysRemaining <= 30) {
        console.log(11111111111)
        return { 'highlight-row': true, 'top': '0' }; // 
      }

      if (daysRemaining < 0) {
        console.log(11111111111)
        return { 'gray-row': true, 'bottom': '0' }; // 
      }

      return '';// 
    }
  },
};
</script>

