<template>
  <div>
   <!-- header -->
   <el-container>
      <el-header class="el-header">
        <home-header />
      </el-header>
    </el-container>
      <div class = "center-container">
        <el-card class="ps-card">
          <h1></h1>
            <el-form label-width="80px" visible.sync="dialogVisible" @close="resetForm('form')">
              <span>
                <el-form ref="form" :model="passwordForm" label-width="80px" :rules="changePasswordRules">
                  <el-form-item type="password" label="" prop="password">
                    <el-input type="password" v-model="passwordForm.password" autocomplete="off" placeholder=""></el-input>
                  </el-form-item>
                  <el-form-item type="password" label="" prop="newPassword">
                    <el-input type="password" v-model="passwordForm.newPassword" autocomplete="off" placeholder=""></el-input>
                  </el-form-item>
                  <el-form-item type="password" label="" prop="confirmPassword">
                    <el-input type="password" v-model="passwordForm.confirmPassword" autocomplete="off" placeholder=""></el-input>
                  </el-form-item>
                </el-form>
              </span>
            </el-form>
          <span>
            <span slot="footer" class="dialog-footer">
                <el-button type="primary" @click="onSubmit"></el-button>
                <el-button type="danger" @click="exit"></el-button>
            </span>
          </span>
        </el-card>
      </div>
    </div>
  </template>
  
  <script>
  import HomeHeader from '../../../components/HomeHeader.vue';
  export default {
    components: { HomeHeader },
    data() {
      let validatePass = (rule, value, callback) => {
        if (value !== this.passwordForm.password) {
          callback(new Error(''))
        } else {
          callback()
        }
      }
      let validateNewPassword = (rule, value, callback) => {
        if (value === this.passwordForm.password) {
          callback(new Error(''))
        } else {
          callback()
        }
      }
      
      let validateNewPassword2 = (rule, value, callback) => {
        if (value !== this.passwordForm.newPassword) {
          callback(new Error('!'))
        } else {
          callback()
        }
      }
      return {
        form: {
          password:""
        },
        passwordForm: {
          id: "",
          password: "",
          newPassword: "",
          confirmPassword: "",
        },
        tableData: [],
        user: localStorage.getItem("user") ? JSON.parse(localStorage.getItem("user")) : {},
        dialogVisible: false,
        changePasswordRules: {
          //
          password: [
            { required: true, message: "", trigger: "blur" },
            { validator: validatePass, message: "", trigger: "blur" },
          ],
          newPassword: [
            { required: true, message: "", trigger: "blur" },
            { validator: validateNewPassword, trigger: 'blur' },
            {
              min: 6,
              max: 15,
              message: "615",
              trigger: "blur",
            },
          ],
          confirmPassword: [
            { required: true, message: "", trigger: "blur" },
            { validator: validateNewPassword2, trigger: 'blur' }
            // { validator: checkpass, trigger: "blur" },
          ],
        },
      }
    },
    created() {
      this.request.get("/user/username/" + this.user.loginName).then(res => {
     
        if (res.code === '200') {
          
          this.form = res.data;
          this.passwordForm.id = res.data.id;
        }
      })
    },
    methods: {
      exit() {
        this.$router.push({ path: '/home' })
      },
      changePassword() {
        this.dialogVisible = true;
        this.passwordForm = {};
      },
      onSubmit() {
        // 
        if (this.passwordForm.newPassword === this.passwordForm.password) {
          this.$message.error('');
          return;
        }
        //
        if(this.passwordForm.newPassword !== this.passwordForm.confirmPassword){
          this.$message.error('');
          return;
        }
        this.dialogVisible = false;
        this.form.password = this.passwordForm.newPassword;
        this.request.post("/user/password" , this.passwordForm).then(res => {
        if (res.code === '200') {
          this.form = res.data;
          // localStorage.clear();
          this.$message.success('')
          sessionStorage.clear()
          this.$router.push({ path: '/login' })
        }else{
          this.$message.error(res.msg)
        }
      })
      },
      resetForm(formName) { //
        this.dialogVisible = false;
        this.$refs[formName].resetFields();
      },
    },
  }
  </script>
  
<style>
  .text {
    font-size: 20px;
  }
  
  .item {
    padding: 18px 0;
  }
  

  .center-container {
    display: flex;
    align-items: center;
    justify-content: center;
    text-align: center;
  }

  .ps-card {
    width: 800px;
    height: 400px;
    position: absolute;
    transform: translate(4%, 5%);
    top: 20%;
  }
</style>