<script>
import HomeHeader from "../../../components/HomeHeader.vue";
import HomeFooter from "../../../components/HomeFooter.vue";
import FullCalendar from '@fullcalendar/vue'
import dayGridPlugin from '@fullcalendar/daygrid'
import timeGridPlugin from '@fullcalendar/timegrid'
import interactionPlugin from '@fullcalendar/interaction'
import moment from 'moment';

export default {
  components: { HomeHeader, HomeFooter, FullCalendar },
  data() {
    return {
      searchForm: {
        type: "",
        orderPlace: ""
      },
      pickerOptions: {
        disabledDate(time) {
          // 
          const today = new Date();
          today.setHours(0, 0, 0, 0);

          // 
          return time.getTime() < today.getTime();
        },
      },
      isOrderCenterSelected: false,
      orderPlace: [],
      dialogVisible: false,
      tableData: [],
      // pickTime: '',
      pageNum: 1,
      pageSize: 10,

      form: {
        userName: "",
        userId: "",
        customerName: "",
        orderPlace: "",
        startTime: "",
        type: "",
        endTime: "",
        pickTime: ''
      },
      user: localStorage.getItem("user")
        ? JSON.parse(localStorage.getItem("user"))
        : {},
      value: '',
      rules: {
        //
        userName: [
          { required: true, message: "", trigger: "blur" },
        ],
        userId: [
          { required: true, message: "", trigger: "blur" },
        ],
        customerName: [
          { required: true, message: "", trigger: "blur" },
        ],
        orderPlace: [
          { required: true, message: "", trigger: "blur" },
        ],
        type: [
          { required: true, message: "", trigger: "blur" },
        ],
        pickTime: [
          { required: true, type: 'array', message: '', trigger: "change" },
        ]
      },
      isDateValid: "",
      isOrderPlaceValid: "",
      matchingItem: [],
      matchingDateItem: [],
      timePeriods: [],
      selectedOrderPlace: "",

      // pickerOptions: {
      //   disabledDate: this.isDateDisabled // 
      // },
      //
      queryStartDate: null,
      queryEndDate:null,

      //
      showDialog: false,
      //
      selectedEvent: null,
      //
      drawer: false,
      calendarOptions: {
        // 
        plugins: [dayGridPlugin, timeGridPlugin, interactionPlugin],
        initialView: 'timeGridWeek',
        locale: 'zh-cn',
        headerToolbar: {
          left: 'prev, next today',
          center: 'title',
          right: 'dayGridMonth,timeGridWeek,timeGridDay',
        },
        buttonText: {
          today: '',
          month: '',
          week: '',
          day: '',
          list: ''
        },
        initialEvents: [],
        events: [],
        // 
        editable: true,
        // 
        selectable: true,
        // 
        select: this.handleDateSelect,
        // 
        eventClick: this.handleEventClick,
        // 
        eventsSet: this.handleEvents,
        //  
        allDayText: '',
        // 
        slotMinTime: '00:00:00',
        // 
        slotMaxTime: '23:59:59',
        // 
        //editable: false
        slotDuration: '01:00:00',
        height: '3.62rem'
      },
    };
  },
  created() {

    this.load();
    const u = localStorage.getItem("user") ? JSON.parse(localStorage.getItem("user")) : {};
    this.form.userName = u.loginName;
    this.form.userId = u.id;
  },
  methods: {
    getAllSquare1() {
      this.request.get("/square/type", {
        params: {
          type: this.form.type
        }
      }).then(res => {
        // console.log("hideField")
        // console.log(res)
        this.orderPlace = res.data;
        this.isOrderCenterSelected = true;
        this.load();
        this.handleOrderPlaceChange1();
      });
    },
    getAllSquare2() {
      this.request.get("/square/type", {
        params: {
          type: this.searchForm.type
        }
      }).then(res => {
        // console.log("hideField")
        // console.log(res)
        this.orderPlace = res.data;
        this.isOrderCenterSelected = true;
        this.load();
        this.handleOrderPlaceChange2();
      });
    },
    handleOrderPlaceChange1() {
      this.selectedOrderPlace = this.form.orderPlace;
    },
    handleOrderPlaceChange2() {
      this.selectedOrderPlace = this.searchForm.orderPlace;
    },
    reset() {
      this.searchForm.orderPlace = ""
      this.searchForm.type = ""

      this.load()
    },
    load2() {
      this.pageNum = 1
      this.load()
    },
    load() {
      this.request.get("/equipmentOrder/pageByStatus", {
        params: {
          pageNum: this.pageNum,
          pageSize: this.pageSize,
          type: this.searchForm.type,
          orderPlace: this.searchForm.orderPlace,
        }

      }).then(
        res => {
          this.tableData = res.data.records
          this.total = res.data.total
          const event = this.tableData.map(event => {
            // console.log(event);
            let typeText = event.type;
            if (event.type === 'second') {
              typeText = 'EMC';
            } else if (event.type === 'third') {
              typeText = '';
            }
            let backColor = "#5580ee"
            if (event.status === "") {
              backColor = "#87d068"
              // backColor = "#87d068"
            }
            else if (event.status === "") {
              backColor = "#409EFF"
            }

            return {
              id: event.id,
              title: typeText + "" + event.orderPlace,
              start: moment(event.startTime).format('YYYY-MM-DDTHH:mm:ss'),
              end: moment(event.endTime).format('YYYY-MM-DDTHH:mm:ss'),
              context: event.userName,
              type: event.type,
              backgroundColor: backColor,
            };
          });
          // console.log(this.calendarOptions.events)
          this.calendarOptions.events = event;
        }
      )
    },
    isDateBeforeToday(date, time) {
      const mealDateTime = new Date(`${date} ${time}`);
      const now = new Date();
      return mealDateTime <= now;
    },
    handleOpen(key, keyPath) {
      // console.log(key, keyPath);
    },
    handleClose(key, keyPath) {
      // console.log(key, keyPath);
    },
    formatDateTime(dateTime) {
      return new Date(dateTime).toLocaleString();
    },
    addOrder() {


      // this.axios.post("http://localhost:9090/equipmentOrder", this.form).then(res => {   //res
      this.$refs.form.validate((valid) => {
        if (valid) {
          // console.log(this.pickTime)

          this.form.startTime = moment(this.form.pickTime[0]).format('YYYY-MM-DD HH:mm:ss');
          this.form.endTime = moment(this.form.pickTime[1]).format('YYYY-MM-DD HH:mm:ss');
          console.log('form', this.form)
          this.request.post("/equipmentOrder", this.form).then(res => {

            if (res.code === "200") {
              this.$message.success("")
              this.$router.push("/deviceorderhome/mydeviceorder")
            } else if (res.code === "402") {
              this.$message.error(res.msg)
            }
          })
        } else {
          return false;
        }
      })
    },
    tomydeviceorder() {
      this.$router.push({ path: '/deviceorderhome/mydeviceorder' })
    },
    isDateDisabled(time) {
      //  time 
      //  true
      //  false
      return this.existingTimeRanges.some(range => {
        return time >= new Date(range.startTime) && time <= new Date(range.endTime);
      });
    },
    /* */
    queryRange() {
        let calendarApi = this.$refs.FullCalendar.getApi();
        // 
        if (this.queryStartDate != null && this.queryEndDate != null) {
        // 
        calendarApi.gotoDate(this.queryStartDate); // 
        // 
        let endDate = new Date(this.queryEndDate);
        //  FullCalendar 
        calendarApi.changeView('timeGrid', {
          start: this.queryStartDate,
          end: endDate,
          
        });
      } else {
        // 
        calendarApi.today();
      }
    },
    /*  */
    toClick() {
      //  FullCalendar 
      const calendar = this.$refs.FullCalendar.getApi();
      // 
      const startDate = moment("2015-06-06");
      const endDate = moment("2028-06-08");
      // 
      const events = calendar.getEvents();
      // 
      const filteredEvents = events.filter(event => {
        // 
        const eventStart = moment(event.start);
        const eventEnd = moment(event.end);
        // 
        return eventStart.isBetween(startDate, endDate, null, '[]') || eventEnd.isBetween(startDate,
          endDate, null, '[]');
      });
      // 
      // console.log(filteredEvents);
    },
    /* */
    handleEventClick(info) {
      // 
      const event = info.event;
      // 
      this.selectedEvent = {
        //
        title: event.title,
        type: event.extendedProps.type,
        start: moment(event.start).format('YYYY-MM-DD HH:mm'),
        end: moment(event.end).format('YYYY-MM-DD HH:mm'),
        // backgroundColor:"#ff99b3",
        // color:"#87d068",
        zw: event.extendedProps.zw,
        //
        context: event.extendedProps.context,
        age: event.extendedProps.age,
        sex: event.extendedProps.sex
      };
      // console.info(info)
      // console.info(this.selectedEvent)
      //
      this.showDialog = true
    },
    /* */
    closeDialog() {
      this.showDialog = false
    }
  },
};
</script>
