<template>
  <div class="login_container" style="background-size: cover; background-position: center;">
 
    <div class="login_box" style="background: rgba(255, 255, 255, 0.5);">
      <!--  -->
      <div class="abc">     </div>
      <div class="avatar_box">
        <img src="../assets/sanfangdianqi.png" alt="" class="logo_image" style="width: 160%; height: 40%;">
      </div>
      
      <!--  -->
      <el-form ref="loginFormRef" :model="loginForm" :rules="loginFormRules" label-width="0px" class="login_form">
        <!--  -->
        <!-- <el-form-item prop="loginName">
          <el-input v-model="loginForm.loginName" prefix-icon="iconfont icon-user" placeholder="" ></el-input>
        </el-form-item> -->
     
        <el-form-item prop="loginName">
          <el-autocomplete :hide-loading="true" :highlight-first-item="true" v-model="loginForm.loginName" :fetch-suggestions="fetchLoginName" 
          prefix-icon="iconfont icon-user" placeholder=""
           style="width: 405px;"></el-autocomplete>
        </el-form-item>

        <!--  -->
        <el-form-item prop="password">
          <el-input v-model="loginForm.password" prefix-icon="iconfont icon-3702mima" type="password" placeholder=""></el-input>
        </el-form-item>
        <!--  -->
      <el-form-item class="btns" style="display: flex; justify-content: center;">
        <el-button type="primary" @click="login" @keyup.enter="login()"></el-button>
        <el-button type="info" @click="resetLoginForm"></el-button>
      </el-form-item>
      </el-form>
    </div>
  </div>
  
</template>
