<script>
import { setRoutes } from '@/router';
export default {
  data() {
    return {
      //
      loginForm: {
        loginName: '',
        password: '',
      },
      // savedUsers: ["",""],
      savedUsers: [],

      //
      loginFormRules: {
        //
        loginName: [
          { required: true, message: "", trigger: "submit" },
          {
            min: 2,
            max: 10,
            message: "310",
            trigger: "submit",
          },
        ],
        //
        password: [
          { required: true, message: "", trigger: "submit" },
          {
            min: 6,
            max: 15,
            message: "615",
            trigger: "submit",
          },
        ],
      },
    };
  },

  mounted() {
    // 
    window.addEventListener("keydown", this.keyDown);
     // 
      // localStorage
    const users = localStorage.getItem('savedUsers');
    // this.savedUsers = users ? JSON.parse(users) : [];
    if (users) {
        this.savedUsers = users.split(',');
      }
    // if (users) {
    //     this.savedUsers = JSON.parse(users);
    //     // this.loginForm.loginName = this.loginForm.loginName = this.savedUsers[0] || '';
    //   }
    // selectedUser
    //  this.loginForm.loginName = this.savedUsers[0] || '';
  },
 
  destroyed() {
    // 
    window.removeEventListener("keydown", this.keyDown, false);
  },
  methods: {
    // https://blog.csdn.net/qq_54896898/article/details/124272111
    fetchLoginName(queryString, cb) {
      try{
      // const users =  JSON.parse(this.savedUsers);
      // const users =  this.savedUsers;
      const users = JSON.parse(localStorage.getItem('savedUsers'))
      const results = queryString ? users.filter(this.createFilter(queryString)) : users;
    //  callback 
      cb(results);}
      catch (error) {
      // Output the error to the console
      console.error('An error occurred:', error);
    }
    },
    createFilter(queryString) {
      return (user) => {
        // return user.toLowerCase().indexOf(queryString.toLowerCase()) === 0;
        return (user.value.toLowerCase().indexOf(queryString.toLowerCase()) === 0)
      };
    },
    //
    resetLoginForm() {
      this.$refs.loginFormRef.resetFields();
    },
    login() {
      this.$refs.loginFormRef.validate((valid)=>{
        if(valid){
          this.request.post('/user/login', this.loginForm).then((resp) => {
        let data = resp;
        if (data.code === '200') {
          // console.log(data)
          //
          // this.startSession();
            sessionStorage.setItem("data", JSON.stringify(data.data)),
            // localStorage.setItem("backMenus", JSON.stringify(data.data.backMenus)),

            sessionStorage.setItem("backMenus", JSON.stringify(data.data.backMenus)),
            localStorage.setItem("user", JSON.stringify(data.data)),
            localStorage.setItem("issuer", JSON.stringify(data.data.loginName));
            //
            // 
            const  saveLoginName  = this.loginForm.loginName;
            // console.log(saveLoginName)

            const noArr = []
            const cnoOrName = ""
            noArr.push({ value: saveLoginName, type: cnoOrName });
            if (JSON.parse(localStorage.getItem("savedUsers"))) {
              if (localStorage.getItem("savedUsers").indexOf(saveLoginName) === -1) {
                // this.savedUsers.push(saveLoginName);
                if (JSON.parse(localStorage.getItem("savedUsers")).length >= 10) {
                  let arr = JSON.parse(localStorage.getItem("savedUsers"));
                  arr.pop();
                  //pop
                  localStorage.setItem("savedUsers", JSON.stringify(arr));
                }
              localStorage.setItem(
                "savedUsers",
                JSON.stringify(
                  noArr.concat(JSON.parse(localStorage.getItem("savedUsers")))
                )
                )
              }
            }else {
            // 
            localStorage.setItem("savedUsers", JSON.stringify(noArr));
          }
            
            //
            setRoutes()
            this.$message({
              message: '',
              type: 'success'
            });
          this.$router.push({ path: '/home' })
          
        } else {
          this.$message({
            message: '',
            type: 'error'
          });
        }
      }).catch((error) => {
      this.$message({
        message: '',
        type: 'error'
      });
    });
        }
        else{
          return false;
        }
      })
  
    },
    keyDown(e) {
      //  enterASCII13
      if (e.keyCode === 13) {
        this.login(); // 
      }
    },
    // 
  // startSession(){
  //   const expirationTime = 2 * 60 * 60 * 1000; // 2
  //   setTimeout(this.expireSession, expirationTime);
  // },
  // expireSession() {
  // //  sessionStorage 
  // sessionStorage.clear();

  // this.$message({
  //             message: '',
  //             type: 'error'
  //           });
  // // 
  // this.$router.push({ path: '/login' })
  // }

    
  },
  // watch: {
  //   savedUsers(newValue) {
  //     // localStorage
  //     localStorage.setItem('savedUsers', JSON.stringify(newValue));
  //   }
  // }
};
</script>
  
