      this.load();
    },
    handleSizeChange(pageSize) {
      this.pageSize = pageSize;
      this.load();
    },
    handleClose(key, keyPath) {
    },
    handleEdit(row) {
      this.modalType = 1;
      // 
      this.form = JSON.parse(JSON.stringify(row));
      this.dialogVisible = true;
    },
    handleDelete(id) {
      console.log("id",id)
      this.$confirm("", "", {
        confirmButtonText: "",
        cancelButtonText: "",
        type: "warning",
      })
        .then(() => {
          this.request.delete("/equipmentOrder/" + id).then((res) => {
            if (res.code === "200") {
              this.$message({
                type: "success",
                message: "!",
              });
            } else {
              this.$message({
                type: "warning",
                message: "!",
              });
            }

            // 
            this.load();
          });
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: "",
          });
        });
    },
    todeviceorder() {
      this.$router.push({ path: '/deviceorderhome' })
    },
    handleDatePickerChange() {
      this.form.timePeriod = ''; // 

      const selectedDate = this.form.startTime;
      const options = { year: 'numeric', month: '2-digit', day: '2-digit' };

      const formattedDate = selectedDate.toLocaleDateString('zh-CN', options).replace(/\//g, '-');

      if (this.isOrderPlaceValid) {
        this.matchingDateItem = this.matchingItem.filter(item => item.startTime === formattedDate);
        if (this.matchingDateItem.length > 0) {
          this.isDateValid = true;
          this.timePeriods = this.matchingDateItem.map(item => item.timePeriod);
          this.timePeriods = Array.from(this.timePeriods);
          this.timePeriods.forEach((timePeriod) => {
            //  timePeriod 
            this.options.forEach(option => {
              option.disabled = timePeriod.toString() === option.value.toString()
            });
            // 
          });
        } 

        this.isOrderPlaceValid = "";
        this.isDateValid = "";
      }
    },
