<template>
    <div class="manage">
        <!--  -->
        <el-form :inline="true" :model="roleForm" class="manage-header">
            <!--  -->
            <el-form-item>
                <el-button @click="handleAdd" type="primary"></el-button>
            </el-form-item>
            <!--  -->
            <el-form-item>
                <el-button @click="handleBatchDelete" style="margin-left: 10px;" type="danger"></el-button>
            </el-form-item>
            <!--  -->
            <el-form-item style="margin-left: 500px;">
                <el-input placeholder="" v-model="roleForm.name"></el-input>
            </el-form-item>
            <el-form-item>
                <el-button @click="load2" type="primary"></el-button>
            </el-form-item>
        </el-form>
        <!--  -->
        <el-dialog title="" :visible.sync="dialogVisible" width="50%" :before-close="handleClose">
            <!--  -->
            <el-form ref="form" :rules="rules" :inline="true" :model="form"  label-width="80px">
                <el-form-item label="" prop="name">
                    <el-input placeholder="" v-model="form.name"></el-input>
                </el-form-item>
                <el-form-item label="" prop="endTime">
                    <el-date-picker v-model="form.endTime" type="date" placeholder="">
                    </el-date-picker>
                </el-form-item>
                <el-form-item label="" prop="remark">
                    <el-input placeholder="" v-model="form.remark"></el-input>
                </el-form-item>
            </el-form>
            <span slot="footer" class="dialog-footer">
                <el-button @click="cancel"> </el-button>
                <el-button type="primary" @click="submit"> </el-button>
            </span>
        </el-dialog>

         <!--  -->
         <el-dialog title="" :visible.sync="menuDialogVisible" width="30%" :before-close="handleCloseRoleMenu">
            <!--  -->
            <el-tree
                :props="props"
                :data="menuData"
                show-checkbox
                check-strictly="true"
                node-key="id"
                ref="tree"
                :default-expanded-keys="expends"
                :default-checked-keys="checks"
             >
            </el-tree>
            <span slot="footer" class="dialog-footer">
                <!-- TODO: -->
                <el-button @click="cancelRoleMenu"> </el-button>
                <el-button type="primary" @click="submitRoleBackMenu"> </el-button>
            </span>
        </el-dialog>

        <!--  -->
        <el-table height="90%" :data="tableData" style="width: 100%" border stripe
            @selection-change="handleSelectionChange">
            <el-table-column type="selection" width="55" align="center"></el-table-column>
            <el-table-column type="index" label="" width="60" align="center" :index="indexMethod"></el-table-column>
            <el-table-column prop="name" label="" align="center"></el-table-column>
            <el-table-column prop="endTime" label="" align="center"></el-table-column>
            <el-table-column prop="remark" label="" align="center"></el-table-column>
            <el-table-column prop="enable" label="" align="center" width="80">
                <template slot-scope="scope">
                    <el-switch v-model="scope.row.enable" active-color="#13ce66" inactive-color="#ccc"
                        @change="changeEnable(scope.row)"></el-switch>
                </template>
            </el-table-column>
            <el-table-column label="" width="280" align="center">
                <template slot-scope="scope">
                   
                    <el-button style="margin-left:5px" size="mini" type="success" @click="selectMenu(scope.row.id)">
                        <i class="el-icon-menu"></i>
                    </el-button>
                    <el-button style="margin-left:5px" size="mini" type="danger" @click="handleDelete(scope.row.id)">
                    </el-button>
                </template>
            </el-table-column>
        </el-table>
        <!--  -->
        <div style="padding: 10px 0">
            <el-pagination @size-change="handleSizeChange" @current-change="handleCurrentChange" :current-page="pageNum"
                :page-sizes="[2, 5, 10, 20]" :page-size="pageSize" layout="total, sizes, prev, pager, next, jumper"
                :total="total">
            </el-pagination>
        </div>

    </div>
</template>

<script>
export default {
    data() {
        return {
            tableData: [],
            modelType: 0,//01,
            multipleSelection: [],
            total: 0,//
            pageNum: 1,
            pageSize: 10,
            roleForm: {
                name: "",
            },
            form: {
                name: "",
                endTime: "",
                remark: "",
                enable: "",
            },
            dialogVisible: false,
            menuDialogVisible: false,
            rules: {
                name: [
                    { required: true, message: "", trigger: "blur" }
                ],
                endTime: [
                    { required: true, message: "", trigger: "blur" }
                ],
            },
            props: {
                label:'name',
            },
            expends:[],
            checks:[],
            roleId: 0,
            menuData:[],
        }
    },
    created() {
        this.load()
    },
    methods: {

        //
        selectMenu(roleId){
            this.menuDialogVisible = true
              //
            this.roleId = roleId
            this.request.get("/backMenu").then(res => {
                this.menuData = res.data
                // id
                this.expends = this.menuData.map(v=> v.id)
            })

            //
            this.request.get("/roleBackMenu/"+this.roleId).then(res => {
              
                this.checks = res.data
            })
        },
        submitRoleBackMenu(){
       
            this.request.post("/roleBackMenu/"+this.roleId,this.$refs.tree.getCheckedKeys()).then(res => {
                    // console.log(res)
                    if (res.code === "200") {
                        this.$message.success("")
                        this.menuDialogVisible = false
                    }else{
                        this.$message.error(""+res.msg)
                        
                    }
            })
        },
    //     handleCheckChange(data, checked, indeterminate) {
    //     // console.log(data, checked, indeterminate);
    //   },

        indexMethod(index) {
            let curpage = this.pageNum   //
            let limitpage = this.pageSize   //
            return (index + 1) + (curpage - 1) * limitpage
        },
        submit() {
            this.$refs.form.validate((valid)=>{
            if(valid){
            this.request.post("/role", this.form).then(res => {   //res
                if (res.code === "200") {
                    this.$message.success("")
                    this.dialogVisible = false
                    this.load()
                } else {
                    this.$message.error("")
                }
            })
            }else{
                     return false;                    
                    }
                    })
        
        },
        cancel() {
            this.handleClose()
        },
        handleClose() {
            this.$refs.form.resetFields()
            this.dialogVisible = false
        },

        cancelRoleMenu(){
            this.handleCloseRoleMenu()
        },
        handleCloseRoleMenu() {
            this.$refs.tree.setCheckedKeys([])
            this.menuDialogVisible = false
        },
        handleAdd() {
            this.modelType = 0
            this.dialogVisible = true  //
            this.form = {}
        },
        handleDelete(id) {
            this.$confirm(', ?', '', {
                confirmButtonText: '',
                cancelButtonText: '',
                type: 'warning'
            }).then(() => {
                this.request.delete("/role/" + id).then(res => {
                    if (res.code === '200') {
                        this.$message({
                            type: 'success',
                            message: '!'
                        });
                    }
                    // 
                    this.load()
                })

            }).catch(() => {
                this.$message({
                    type: 'info',
                    message: ''
                });
            });
        },
        handleSelectionChange(val) {
            console.log(val)
            this.multipleSelection = val
        },
        handleBatchDelete() {
            if (this.multipleSelection.length === 0) {
                this.$message.warning("");
                return;
            }
            let ids = this.multipleSelection.map(v => v.id)  //[{},{}...]id[1.2.3]
            this.$confirm(', ?', '', {
                confirmButtonText: '',
                cancelButtonText: '',
                type: 'warning'
            }).then(() => {
                this.request.post("/role/del/batch", ids).then(res => {
                    if (res.code === '200') {
                        this.$message({
                            type: 'success',
                            message: '!'
                        });
                    }
                    // 
                    this.load()
                })

            }).catch(() => {
                this.$message({
                    type: 'info',
                    message: ''

                });
                this.load()
            });
        },
        load2(){
            this.pageNum = 1
            this.load()
        },
        load() {
            this.request.get("/role/page", {
                params: {
                    pageNum: this.pageNum,
                    pageSize: this.pageSize,
                    name: this.roleForm.name
                }
            }).then(res => {
                this.tableData = res.data.records
                this.total = res.data.total

            })
            
          
            // fetch("http://localhost:9090/role/page?pageNum=" + this.pageNum + "&pageSize=" + this.pageSize + "&name=" + this.roleForm.name)
            //     .then(res => res.json()).then(
            //         res => {
            //             // console.log(res)
            //             this.tableData = res.data.records
            //             this.total = res.data.total
            //         }
            //     )
        },
        handleCurrentChange(pageNum) {
            this.pageNum = pageNum
            this.load()
        },
        handleFileUploadSuccess() {
            this.$message.success("")
            this.load()
        },
        handleSizeChange(pageSize) {
            //   console.log(pageSize)
            this.pageSize = pageSize
            this.load()
        },
        changeEnable(row) {
            // console.log("1111")
            this.request.put("/role", row).then(res => {
                if (res.code === '200') {
                    this.$message.success("")
                } else {
                    this.$message.error("")
                }
            })
        },
    },
};
</script>

<style lang="less" scoped>
.manage {
    height: 90%;

    .manage-header {
        display: flex;
        // justify-content: space-between;
        align-items: center;
    }

    .common-table {
        position: relative;
        height: calc(100% - 62px);

        .pager {
            // position: absolute;
            // bottom: 0;
            // right: 20px;
            padding: 10px 0;
        }
    }
}
</style>