<template>
    <div class="manage">
        <!--  -->
        <el-form :inline="true" :model="userForm" class="manage-header">
            <!--  -->
            <!-- <el-form-item>
                <el-upload action="http://localhost:9090/order/upload" :show-order-list="false"
                    :on-success="handleFileUploadSuccess" style="display: inline-block">
                    <el-button type="primary" class="ml-5"></el-button>
                </el-upload>
            </el-form-item> -->
            <!--  -->
            <el-form-item>
                <el-button @click="handleBatchDelete" style="margin-left: 10px;" type="danger">
                    
                </el-button>
            </el-form-item>
            <!--form-->
            <el-form-item style="margin-left: 500px;">
                <el-input placeholder="" v-model="userForm.orderUser"></el-input>
            </el-form-item>
            <el-form-item>
                <el-button @click="load2" type="primary"></el-button>
                <el-button type="warning" @click="reset"></el-button>
            </el-form-item>
        </el-form>
        <!--  -->
        <el-table  :data="tableData" style="width: 100%" border stripe
            @selection-change="handleSelectionChange">
            <el-table-column type="selection" width="55" align="center"></el-table-column>
            <el-table-column type="index" label="" width="60" align="center" :index="indexMethod"></el-table-column>
            <el-table-column prop="mealDate" label="" align="center"></el-table-column>
            <el-table-column prop="mealTime" label="" align="center"></el-table-column>
            <el-table-column prop="orderUser" label="" align="center"></el-table-column>
            <el-table-column prop="tel" label="" align="center"></el-table-column>
            <el-table-column prop="orderNum" label="" align="center"></el-table-column>
            <!-- <el-table-column prop="enable" label="" align="center" width="80">
                <template slot-scope="scope">
                    <el-switch v-model="scope.row.enable" active-color="#13ce66" inactive-color="#ccc"
                        @change="changeEnable(scope.row)"></el-switch>
                </template>
            </el-table-column> -->
            <el-table-column label="" width="200" align="center">
                <template slot-scope="scope">
                    <el-button style="margin-left:5px" size="mini" type="danger" @click="handleDelete(scope.row.id)">
                    </el-button>
                </template>
            </el-table-column>
        </el-table>
        <!--  -->
        <div style="padding: 10px 0">
            <el-pagination @size-change="handleSizeChange" @current-change="handleCurrentChange" :current-page="pageNum"
                :page-sizes="[2, 5, 10, 20]" :page-size="pageSize" layout="total, sizes, prev, pager, next, jumper"
                :total="total">
            </el-pagination>
        </div>
    </div>
</template>

<script>
export default {
    data() {
        return {
            tableData: [],
            modelType: 0,//01,
            multipleSelection: [],

            total: 0,//
            pageNum: 1,
            pageSize: 10,
            userForm: {
                orderUser: '',
            }
        }
    },
    created() {
        this.load()
    },
    methods: {
        indexMethod(index) {
            let curpage = this.pageNum   //
            let limitpage = this.pageSize   //
            return (index + 1) + (curpage - 1) * limitpage
        },
        handleDelete(id) {
            this.$confirm(', ?', '', {
                confirmButtonText: '',
                cancelButtonText: '',
                type: 'warning'
            }).then(() => {
                this.request.delete("/order/back/" + id).then(res => {
                    if (res.code === '200') {
                        this.$message({
                            type: 'success',
                            message: '!'
                        });
                    }
                    // 
                    this.load()
                })

            }).catch(() => {
                this.$message({
                    type: 'info',
                    message: ''
                });
            });
        },
        handleSelectionChange(val) {
            console.log(val)
            this.multipleSelection = val
        },
        handleBatchDelete() {
            if (this.multipleSelection.length === 0) {
                this.$message.warning("");
                return;
            }

            let ids = this.multipleSelection.map(v => v.id)  //[{},{}...]id[1.2.3]
            this.$confirm(', ?', '', {
                confirmButtonText: '',
                cancelButtonText: '',
                type: 'warning'
            }).then(() => {
                this.request.post("/order/del/batch", ids).then(res => {
                    if (res.code === '200') {
                        this.$message({
                            type: 'success',
                            message: '!'
                        });
                    }
                    // 
                    this.load()
                })

            }).catch(() => {
                this.$message({
                    type: 'info',
                    message: ''

                });
                this.load()
            });
        },
        load2(){
            this.pageNum = 1
            this.load()
        },
        load() {

            this.request.get("/order/pageRecord" , {
                params: {
                pageNum: this.pageNum,
                pageSize: this.pageSize,
                orderUser: this.userForm.orderUser
                }
            }).then(
                    res => {
                        console.log(res)
                        this.tableData = res.data.records
                        this.total = res.data.total
                    }
                )
        },
        reset() {
            this.userForm.orderUser = ""
            this.load()
        },
        handleCurrentChange(pageNum) {
            this.pageNum = pageNum
            this.load()
        },
        handleFileUploadSuccess() {
            this.$message.success("")
            this.load()
        },
        handleSizeChange(pageSize) {
            //   console.log(pageSize)
            this.pageSize = pageSize
            this.load()
        },
        changeEnable(row) {
            console.log("1111")
            this.request.post("/order", row).then(res => {
                if (res.data.code === '200') {
                    this.$message.success("")
                } else {
                    this.$message.error("")
                }
            })
        },
    },
};
</script>

<style lang="less" scoped>
.manage {
    height: 90%;

    .manage-header {
        display: flex;
        // justify-content: space-between;
        align-items: center;
    }

    .common-table {
        position: relative;
        height: calc(100% - 62px);

        .pager {
            // position: absolute;
            // bottom: 0;
            // right: 20px;
            padding: 10px 0;
        }
    }
}
</style>