<template>
    <div>
        <!--  -->
        <div style="margin: 10px 0">
            <el-form :inline="true">
                <el-form-item>
                    <el-button type="primary" @click="handleAdd" v-if="user.role.includes('')"></el-button>
                </el-form-item>
                <el-form-item>
                    <el-button @click="delBatch" style="margin-left: 10px;" type="danger"></el-button>
                </el-form-item>
                <el-form-item style="margin-left: 600px;">
                    <el-input style="width: 200px" placeholder="" suffix-icon="el-icon-search"
                        v-model="theme"></el-input>
                </el-form-item>
                <el-form-item>
                    <el-button class="ml-5" type="primary" @click="load2"></el-button>
                    <el-button type="warning" @click="reset"></el-button>
                </el-form-item>
            </el-form>
        </div>
        <!--  -->
        <el-table :header-row-style="{ height: '50px' }" :data="tableData" border stripe
            :header-cell-class-name="'headerBg'" @selection-change="handleSelectionChange"
            style="width:1241px;height:700px">
            <el-table-column type="selection" width="70px" align="center"></el-table-column>
            <el-table-column type="index" label="" width="70px" align="center" :index='indexMethod'></el-table-column>
            <el-table-column prop="theme" label="" width="400px" align="center"  show-overflow-tooltip></el-table-column>
            <el-table-column prop="content" label="" width="200px" align="center">
                <template slot-scope="scope">
                    <el-button @click="view(scope.row.content)" type="primary"></el-button>
                </template>
            </el-table-column>
            <el-table-column prop="issuer" label="" width="100px" align="center"></el-table-column>
            <el-table-column prop="createTime" label="" width="200px" align="center"></el-table-column>
            <el-table-column label="" align="center">
                <template slot-scope="scope">
                    <el-button type="primary" size="mini" @click="handleEdit(scope.row)" v-if="user.role.includes('')">
                    </el-button>
                    <el-button style="margin-left:5px" size="mini" type="danger" @click="del(scope.row.id)">
                    </el-button>
                </template>
            </el-table-column>
        </el-table>
        <!--  -->
        <div style="padding: 10px 0">
            <el-pagination @size-change="handleSizeChange" @current-change="handleCurrentChange" :current-page="pageNum"
                :page-sizes="[2, 5, 10, 20]" :page-size="pageSize" layout="total, sizes, prev, pager, next, jumper"
                :total="total">
            </el-pagination>
        </div>
        <!--  -->
        <el-dialog title="" :visible.sync="dialogFormVisible1" width="60%" @close="closeDialog"
            :close-on-click-modal="false">
            <el-form label-width="80px" size="small" ref="noticeFormRef" :model="form" :rules="rules">
                <el-form-item label="" prop="theme">
                    <el-input v-model="form.theme" autocomplete="off"></el-input>
                </el-form-item>
                <el-form-item label="" prop="content">
                    <!-- <mavon-editor ref="md" v-model="form.content" :ishljs="true" @imgAdd="imgAdd" /> -->
                    <div id="editor"></div>
                </el-form-item>
            </el-form>
            <div slot="footer" class="dialog-footer">
                <el-button @click="dialogFormVisible1 = false"> </el-button>
                <el-button type="primary" @click="save"> </el-button>
            </div>
        </el-dialog>
        <el-dialog title="" :visible.sync="dialogFormVisible" width="60%" @close="closeDialog"
            :close-on-click-modal="false">
            <el-form label-width="80px" size="small">
                <el-form-item label="">
                    <el-input v-model="form.theme" autocomplete="off"></el-input>
                </el-form-item>

                <el-form-item label="" prop="content">
                    <div id="editor"></div>
                </el-form-item>
            </el-form>
            <div slot="footer" class="dialog-footer">
                <el-button @click="dialogFormVisible = false"> </el-button>
                <el-button type="primary" @click="update"> </el-button>
            </div>
        </el-dialog>
        <el-dialog title="" :visible.sync="viewDialogVis" width="60%">
            <el-card>
                <div class="w-e-text">
                    <!-- <mavon-editor class="md" ref="mdedit" :value="content" :subfield="false" :defaultOpen="'preview'"
                        :toolbarsFlag="false" :editable="false" :scrollStyle="true" :ishljs="true" /> -->
                    <div v-html="content"></div>
                </div>
            </el-card>
            <div slot="footer" class="dialog-footer">
                <el-button type="primary" @click="viewDialogVis = false"></el-button>
            </div>

        </el-dialog>

    </div>
</template>

<script>
import { mavonEditor } from "mavon-editor";
import "mavon-editor/dist/css/index.css";
import E from "wangeditor"
import hljs from 'highlight.js'
export default {
    components: { mavonEditor },
    data() {
        return {
            form: {
                theme: '',
                content: ''
            },
            tableData: [],
            issuer: localStorage.getItem("issuer") ? JSON.parse(localStorage.getItem("issuer")) : {},
            content: '',
            theme: '',
            multipleSelection: [],
            pageNum: 1,
            pageSize: 10,
            total: 0,
            dialogFormVisible1: false,
            dialogFormVisible: false,
            teachers: [],
            user: localStorage.getItem("user") ? JSON.parse(localStorage.getItem("user")) : {},
            viewDialogVis: false,
            editor: null,
            rules: {
                theme: [
                    { required: true, message: '', trigger: 'blur' }
                ]
            }
        }
    },
    created() {
        this.load()
    },
    methods: {
        indexMethod(index) {
            let curpage = this.pageNum   //
            let limitpage = this.pageSize   //
            return (index + 1) + (curpage - 1) * limitpage
        },
        view(content) {
            this.content = content
            this.viewDialogVis = true
        },
        // @imgAdd event
        imgAdd(pos, $file) {
            let $vm = this.$refs.md
            // ..
            const formData = new FormData();
            formData.append('file', $file);
            this.axios({
                // url: 'http://localhost:9090/file/upload',
                // url: 'http://192.168.0.153:9090/file/upload',
                method: 'post',
                data: formData,
                headers: { 'Content-Type': 'multipart/form-data' },
            }).then((res) => {
                console.log(788787878878)
                console.log(res)
                // .url![...](./0) -> ![...](url)
                $vm.$img2Url(pos, res.data);
            })
        },
        load2() {
            this.pageNum = 1
            this.load()
        },
        load() {
            this.request.get("/news/page", {
                params: {
                    pageNum: this.pageNum,
                    pageSize: this.pageSize,
                    theme: this.theme,
                }
            }).then(res => {
                // console.log(res)
                this.tableData = res.data.records
                this.total = res.data.total
            })
        },
        changeEnable(row) {
            this.request.post("/file/upload", row).then(res => {
                if (res.code === '200') {
                    this.$message.success("")
                }
            })
        },
        setRichText() {
            //
            this.$nextTick(() => {
                this.editor = new E('#editor')


                this.editor.highlight = hljs
                // WangEditor
                // this.editor.config.pasteFilterStyle = false;
                // this.editor.config.excludeMenus = ['head', 'indent', 'justify', 'quote', 'emoticon', 'undo', 'redo'];
                // this.editor.customConfig.pasteTextHandle = function (content) {
                //         return content;
                //         };
                this.editor.config.onkeydown = function (e) {
                    if (e.keyCode === 13 && !e.shiftKey) { //  Shift 
                        e.preventDefault(); // 
                        this.command(e, 'insertHTML', '<br>'); //  <br> 
                    }
                };

                this.editor.config.uploadImgServer = 'http://192.168.1.14:9090/fileEquipment/uploadImg'
                this.editor.config.uploadFileName = "file"

                this.editor.config.uploadImgHeaders = {
                    token: this.user.token
                }
                this.editor.config.uploadImgParams = {
                    dataType: 'img',
                }
                this.editor.config.uploadVideoServer = 'http://192.168.1.14:9090/fileEquipment/uploadImg'
                this.editor.config.uploadVideoName = 'file'
                this.editor.config.uploadVideoHeaders = {
                    token: this.user.token
                }
                this.editor.config.uploadVideoParams = {
                    dataType: 'video',
                }
                this.editor.create()

            })
        },
        handleAdd() {
            this.dialogFormVisible1 = true
            this.form = {}
            this.setRichText()
        },
        closeDialog() {
            //
            this.editor.destroy()
            this.editor = null
        },
        handleEdit(row) {
            this.form = JSON.parse(JSON.stringify(row))
            this.dialogFormVisible1 = true
            this.setRichText()
            setTimeout(() => {
                this.editor.txt.html(row.content) //
            }, 0);


        },
        del(id) {
            this.$confirm(', ?', '', {
                confirmButtonText: '',
                cancelButtonText: '',
                type: 'warning'
            }).then(() => {
                this.request.delete("/news/" + id).then(res => {
                    if (res.code === '200') {
                        this.$message.success("")
                        this.load()
                    } else {
                        this.$message.error("")
                    }
                })
            }).catch(() => {
                this.$message({
                    type: 'info',
                    message: ''
                });
            });
        },
        handleSelectionChange(val) {
            console.log(val)
            this.multipleSelection = val
        },
        delBatch() {
            if (this.multipleSelection.length === 0) {
                this.$message.warning("");
                return;
            }

            let ids = this.multipleSelection.map(v => v.id)  //[{},{}...]id[1.2.3]
            this.$confirm(', ?', '', {
                confirmButtonText: '',
                cancelButtonText: '',
                type: 'warning'
            }).then(() => {
                this.request.post("/news/del/batch", ids).then(res => {
                    if (res.code === '200') {
                        this.$message.success("")
                        this.load()
                    } else {
                        this.$message.error("")
                    }
                })
            }
            )
        },
        save() {
            this.$refs.noticeFormRef.validate((valid) => {
                if (valid) {
                    let content = this.editor.txt.html()
                    this.form.content = content
                    this.request.post("/news/" + this.issuer, this.form).then(res => {
                        if (res.code === '200') {
                            this.$message.success("")
                            this.dialogFormVisible1 = false
                            this.load()
                        } else {
                            this.$message.error("")
                        }
                    })

                } else {
                    return false;
                }
            })
        }
        ,
        update() {
            this.request.put("/news", this.form).then(res => {
                if (res.code === '200') {
                    this.$message.success("")
                    this.dialogFormVisible = false
                    this.load()
                } else {
                    this.$message.error("")
                }
            })
        },
        reset() {
            this.theme = ""
            this.load()
        },
        handleSizeChange(pageSize) {
            this.pageSize = pageSize
            this.load()
        },
        handleCurrentChange(pageNum) {
            this.pageNum = pageNum
            this.load()
        },
        download(url) {
            window.open(url)
        },
    },
};
</script>

