          <home-header />
        </el-header>
      </el-container>
     
      <!--  -->
    <div>
      <div class="box-c"></div>
      <div id="handStyle" class="box-card2">
        <h2 @click="devicelist"></h2>
      </div>
      <div class="box-card3">
        <h2 @click="deviceinspplan"></h2>
      </div>
      <div class="box-card4">
        <h2 @click="periodverificationplanf"></h2>
      </div>
      <div class="box-card10">
        <h2 @click=" FixedAssets"></h2>
      </div>
    </div>
      <!--  -->
  
      <div>
        <el-card   class="table" :header-cell-style="{ color: 'green', fontSize: '24px' }">
          <el-breadcrumb separator-class="el-icon-arrow-right">
                        <el-breadcrumb-item :to="{ path: '/home' }" style="font-size: 0.09rem;"></el-breadcrumb-item>
                        <el-breadcrumb-item :to="{ path: '/devicehome/DeviceInspectPlan' }" style="font-size: 0.09rem;"></el-breadcrumb-item>
      
                    </el-breadcrumb>
          <h1></h1>
          <el-form class="manage-header">
            <!--  -->
         
            <el-form-item style="margin-left: 3.3rem;">
                <el-input placeholder="" v-model="equipForm.name"></el-input>
            </el-form-item>
            <el-form-item>
                <el-button @click="load" type="primary" style="margin-left: 10px;"></el-button>
                <el-button type="warning" @click="reset"></el-button>
            </el-form-item>
        </el-form>
       
          <!--  -->
          <el-table :data="tableData"  border stripe
            @selection-change="handleSelectionChange">
            <el-table-column type="selection" width="55" align="center"></el-table-column>
            <el-table-column type="index" label="" width="60" align="center" :index="indexMethod"></el-table-column>
            <el-table-column prop="equipStatus" label="" align="center"></el-table-column>
            <el-table-column prop="id" label="" align="center"></el-table-column>
            <el-table-column prop="equipType" label="" align="center"></el-table-column>
            <el-table-column prop="name" label="" align="center"></el-table-column>
            <el-table-column prop="department" label="" align="center"></el-table-column>
            <el-table-column prop="price" label="" align="center"></el-table-column>
            <el-table-column prop="specific1" label="" align="center"></el-table-column>
            <el-table-column prop="manuFact" label="" align="center"></el-table-column>
            <el-table-column prop="acceptDate" label="" align="center" ></el-table-column>
            <el-table-column prop="prodDate" label="" align="center" ></el-table-column>
            <el-table-column prop="serialNum" label="" align="center"></el-table-column>
            <el-table-column prop="purchCont" label="" align="center"  show-overflow-tooltip>
             <template slot-scope="scope">
                <el-link type="primary"  @click="handleDownload(scope.row.purchContPath)" >
                {{ scope.row.purchCont }}
                </el-link>
             </template>
            </el-table-column>
            <el-table-column prop="remarks" label="" align="center"></el-table-column>
            <el-table-column prop="country" label="" align="center"></el-table-column>
            <el-table-column prop="unit" label="" align="center"></el-table-column>
            <el-table-column prop="num" label="" align="center"></el-table-column>
            <el-table-column prop="equipFileNum" label="" align="center"></el-table-column>
            <el-table-column prop="fileLoca" label="" align="center"></el-table-column>
            <!-- <el-table-column prop="calibDate" label="" align="center"></el-table-column> -->
            <!-- <el-table-column label="" width="200" align="center" fixed="right">
                <template slot-scope="scope">
                    <el-button style="margin-left:5px" size="mini" type="danger" @click="handleDelete(scope.row.id)">
                    </el-button>
                </template>
            </el-table-column> -->
        </el-table>
          </el-card>
        </div>
        <!--  -->
        <!-- <el-container class="footer">
          <el-footer class="el-footer">
            <home-footer />
          </el-footer>
        </el-container> -->
        <!--  -->
        <div class="page">
          <el-pagination @size-change="handleSizeChange" @current-change="handleCurrentChange" :current-page="pageNum"
            :page-sizes="[2, 5, 10, 20]" :page-size="pageSize" layout="total, sizes, prev, pager, next, jumper"
            :total="total">
          </el-pagination>
        </div>
      </div>
    </template>
    
    <script>
    import HomeHeader from "../../../components/HomeHeader.vue";
    import HomeFooter from "../../../components/HomeFooter.vue";
    export default {
      components: { HomeHeader, HomeFooter },
      data() {
          return {
            dialogVisible: false,
            tableData: [],
            modelType: 0,//01,
            total: 0,//
            pageNum: 1,
            pageSize: 10,
            equipForm: {
                name: '',
                // calibDate: '',
            },
          
           
          }
      },
      created() {
          this.load()
      },
      methods: {
        handleDownload(path){
            let url = "http://192.168.1.14:8012/onlinePreview?url=" + path;
            window.open(url+ '#toolbar=0', '_blank');
        },
        // handleChange(file){
           
        //     this.form.purchCont = file.name
        //     this.$forceUpdate();
        //     // console.log( this.form.purchCont)
        // },
        handleFileUploadSuccess(res) {
            console.log(res)
            // this.form.purchCont=''
            if(res.code === "200" ){
                this.form.purchCont = res.data.name
                this.form.purchContPath = res.data.path
                this.$forceUpdate();
                this.$message.success("")
                // console.log(  res.data.name)
               
                // this.refreshForm(); 
                // console.log(   this.form.purchCont)
            }else{
                this.$message.error("")
            }
          
            // this.load()
        },
        indexMethod(index) {
            let curpage = this.pageNum   //
            let limitpage = this.pageSize   //
            return (index + 1) + (curpage - 1) * limitpage
        },
        //
        submit() {
            this.$refs.formDialog.validate((valid)=>{
                if(valid){
            this.request.post("/equipment", this.form).then(res => {
                if (res.code === "200") {
                    this.$message.success("")
                    this.dialogVisible = false
                    this.load()
                } else {
                    this.$message.error("")
                }
            })
        }else{
                return false;
            }
        })
        },
        handleClose() {
            this.$refs.formDialog.resetFields()
            this.dialogVisible = false
        },
        cancel() {
            this.handleClose()
        },
        //  //
        //  exp(){
        //     window.open("http://192.168.0.153:9090/equipLedger/export")
        //  },

        //  //
        //  handleExcelImportSuccess(){
        //     this.$message.success("")
        //     this.load()
        //  },
        //   //
        // handleExcelImportFail(){
        //     this.$message.error("")
        //     this.load()
        //  },

        //
        reset() {
            this.equipForm.name = ""
            this.load()
        },
        handleEdit(row) {
            this.modalType = 1
            // 
            this.form = JSON.parse(JSON.stringify(row))
            this.dialogVisible = true

        },
        handleDelete(id) {
            this.$confirm(', ?', '', {
                confirmButtonText: '',
                cancelButtonText: '',
                type: 'warning'
            }).then(() => {
                this.request.delete("/equipment/" + id).then(res => {
                    if (res.code === '200') {
                        this.$message({
                            type: 'success',
                            message: '!'
                        });
                    }
                    // 
                    this.load()
                })

            }).catch(() => {
                this.$message({
                    type: 'info',
                    message: ''
                });
            });
        },
        handleSelectionChange(val) {
            console.log(val)
            this.multipleSelection = val
        },
        handleBatchDelete() {
            let ids = this.multipleSelection.map(v => v.id)  //[{},{}...]id[1.2.3]
            this.$confirm(', ?', '', {
                confirmButtonText: '',
                cancelButtonText: '',
                type: 'warning'
            }).then(() => {
                this.request.post("/equipment/del/batch", ids).then(res => {
                    if (res.code === '200') {
                        this.$message({
                            type: 'success',
                            message: '!'
                        });
                    }
                    // 
                    this.load()
                })

            }).catch(() => {
                this.$message({
                    type: 'info',
                    message: ''

                });
                this.load()
            });
        },
        handleAdd() {
            this.modelType = 0
            this.dialogVisible = true  //
            this.form = {}
        },
        load() {
            this.request.get("/equipment/page", {
                params: {
                    pageNum: this.pageNum,
                    pageSize: this.pageSize,
                    name: this.equipForm.name,
                }
            })
                .then(
                    res => {
                        // console.log(res)
                        this.tableData = res.data.records
                        this.total = res.data.total
                    }
                )
        },
        handleCurrentChange(pageNum) {
            this.pageNum = pageNum
            this.load()
        },
        handleSizeChange(pageSize) {
            this.pageSize = pageSize
            this.load()
        },
        changeEnable(row) {      
            this.request.post("/equipment", row).then(res => {
                if (res.data.code === '200') {
                    this.$message.success("")
                } else {
                    this.$message.error("")
                }
            })
        },
        devicelist() {
          this.$router.push({ path: '/devicehome/devicelist' })
        },
        periodverificationplanf(){
          this.$router.push({ path: '/devicehome/periodverificationplanf' })
        },
        FixedAssets(){
        this.$router.push({ path: '/devicehome/FixedAssets' })
      },
      deviceinspplan() {
      this.$router.push({ path: '/devicehome/deviceinspplan' })
    },
      },
    };
    </script>
    
