<template>
    <div class="manage">
        <!--  -->
        <el-dialog title="" :visible.sync="dialogVisible" width="50%" :before-close="handleClose">
            <!--  -->
            <el-form ref="form" :rules="rules" :inline="true" :model="form" label-width="80px">
                <el-form-item label="" prop="id">
                    <el-input placeholder="" v-model="form.id"></el-input>
                </el-form-item>
                <el-form-item label="" prop="loginName">
                    <el-input placeholder="" v-model="form.loginName"></el-input>
                </el-form-item>
                <el-form-item label="" prop="password">
                    <el-input placeholder="" v-model="form.password"></el-input>
                </el-form-item>
                <!-- <el-form-item label="" prop="endTime">
                    <el-date-picker v-model="form.endTime" type="date" placeholder="">
                    </el-date-picker>
                </el-form-item> -->
                <el-form-item label="" prop="role">
                    <el-select v-model="form.role" placeholder="">
                        <el-option v-for="item in role" :label="item.name" @change="change" :value="item.name"
                            :key="item.name"></el-option>
                    </el-select>
                </el-form-item>
            </el-form>
            <!--  -->
            <span slot="footer" class="dialog-footer">
                <el-button @click="cancel"> </el-button>
                <el-button type="primary" @click="submit"> </el-button>
            </span>
        </el-dialog>

        <!--  -->
        <el-dialog title="" :visible.sync="updateDialogVisible" width="50%" :before-close="updateCancel">
            <!--  -->
            <el-form ref="form" :rules="rules" :inline="true" :model="form" label-width="80px">
                <el-form-item label="" prop="id">
                    <el-input placeholder="" v-model="form.id" disabled></el-input>
                </el-form-item>
                <el-form-item label="" prop="loginName">
                    <el-input placeholder="" v-model="form.loginName"></el-input>
                </el-form-item>
                <el-form-item label="" prop="password">
                    <el-input placeholder="" v-model="form.password"></el-input>
                </el-form-item>
                <!-- <el-form-item label="" prop="endTime">
                    <el-date-picker v-model="form.endTime" type="date" placeholder="">
                    </el-date-picker>
                </el-form-item> -->
                <el-form-item label="" prop="role">
                    <el-select v-model="form.role" placeholder="">
                        <el-option v-for="item in role" :label="item.name" @change="change" :value="item.name"
                            :key="item.name"></el-option>
                    </el-select>
                </el-form-item>
            </el-form>

            <!--  -->
            <span slot="footer" class="dialog-footer">
                <el-button @click="updateCancel"> </el-button>
                <el-button type="primary" @click="submitUpdate"> </el-button>
            </span>
        </el-dialog>

        <!--  -->
        <el-form :inline="true" :model="userForm">
            <!--  -->
            <el-form-item>
                <el-button @click="handleAdd" type="primary"></el-button>
            </el-form-item>
            <!--  -->
            <el-form-item>
                <el-button @click="handleBatchDelete" style="margin-left: 10px;" type="danger"></el-button>
            </el-form-item>
            <el-form-item>
                <el-button @click="exp" style="margin-left: 10px;" type="primary"><i class="el-icon-top">
                    </i></el-button>
            </el-form-item>
            <!--form-->
            <el-form-item style="margin-left: 440px; ">
                <el-input placeholder="" v-model="userForm.id"></el-input>
            </el-form-item>
            <!-- <el-form-item>
                <el-button @click="load" type="primary"></el-button>
            </el-form-item> -->
            <el-form-item>
                <el-input placeholder="" v-model="userForm.loginName"></el-input>
            </el-form-item>
            <el-form-item>
                <el-button @click="load2" type="primary"></el-button>
                <el-button type="warning" @click="reset"></el-button>
            </el-form-item>
        </el-form>
        <!--      -->
        <div class="common-table">
            <el-table :row-style="{ height: '55px' }" :data="tableData" style="width: 7.5rem;" border stripe
                @selection-change="handleSelectionChange">
                <el-table-column type="selection" width="55" align="center"></el-table-column>
                <el-table-column type="index" label="" width="70" :index="indexMethod"></el-table-column>
                <el-table-column prop="id" label="" align="center">
                </el-table-column>
                <el-table-column prop="loginName" label="" align="center">
                </el-table-column>
                <!-- <el-table-column prop="password" label="" align="center">
                </el-table-column> -->
                <!-- <el-table-column prop="endTime" label="" align="center"> -->
                <!-- </el-table-column> -->
                <el-table-column prop="role" label="" align="center">
                </el-table-column>
                <!-- <el-table-column prop="enable" label="" align="center" width="80">
                    <template slot-scope="scope">
                        <el-switch v-model="scope.row.enable" active-color="#13ce66" inactive-color="#ccc"
                            @change="changeEnable(scope.row)"></el-switch>
                    </template>
                </el-table-column> -->
                <el-table-column label="" width="300" align="center">
                    <template slot-scope="scope">
                        <!-- <el-button size="mini" type="primary" :disabled="{user}.role !== ''" @click="handleEdit(scope.row)"></el-button> -->
                        <!-- <el-button size="mini" type="primary" :disabled="disableEditButton(scope.row)" @click="handleEdit(scope.row)"></el-button> -->
                        <el-button size="mini" type="primary" @click="handleEdit(scope.row)"></el-button>

                        <el-popconfirm class="ml-5" confirm-button-text="" cancel-button-text="" icon="el-icon-info"
                            icon-color="red" title="" @confirm="del(scope.row.id)">
                        </el-popconfirm>
                        <el-button style="margin-left: 5px" size="mini" type="info"
                            @click="handleView(scope.row.id)"></el-button>
                        <el-button style="margin-left:5px" size="mini" type="warning"
                            @click="resetPassword(scope.row.id)"></el-button>

                        <el-button style="margin-left:5px" size="mini" type="danger"
                            @click="handleDelete(scope.row.id)"></el-button>
                    </template>
                </el-table-column>
            </el-table>
            <user-details-card v-if="viewDialogVisible" :user="viewUser" @close="handleCloseDetails"></user-details-card>
        </div>

        <!--  -->
        <div class="pager">
            <el-pagination @size-change="handeleSizeChange" @current-change="handleCurrentChange" :current-page="pageNum"
                :page-sizes="[2, 5, 10, 20]" :page-size="pageSize" layout="total, sizes, prev, pager, next, jumper"
                :total="total">
            </el-pagination>
        </div>
    </div>
</template>
 
<script>
import UserDetailsCard from './UserDetailsCard.vue';//

export default {
    components: {
        'user-details-card': UserDetailsCard,//
    },
    data() {
        return {
            user: null,
            viewUser: null, //
            dialogVisible: false,
            updateDialogVisible: false,
            viewDialogVisible: false,
            multipleSelection: [],
            form: {
                id: "",
                loginName: "",
                password: "",
                // createTime: "",
                // endTime: "",
            },
            rules: {
                id: [
                    { required: true, message: "", trigger: "blur" }
                ],
                loginName: [
                    { required: true, message: "", trigger: "blur" }
                ],
                password: [
                    { required: true, message: "", trigger: "blur" }
                ],
                endTime: [
                    { required: true, message: "", trigger: "blur" }
                ],
                role: [
                    { required: true, message: "", trigger: "blur" }
                ]
            },
            tableData: [],
            modelType: 0,//01,
            total: 0,//
            pageNum: 1,
            pageSize: 10,
            userForm: {
                id: '',
                loginName: '',
            },
            role: [],
        }
    },
    mounted() {
    },
    created() {
        this.user = localStorage.getItem("user") ? JSON.parse(localStorage.getItem("user")) : {}


        // console.log(this.user)
        //
        this.load()
        this.loadRole()
    },
    methods: {
        disableEditButton(row) {
            return this.user.role !== '';
        },
        //
        reset() {

            this.userForm.loginName = ""
            this.userForm.id = ""
            this.load()
        },
        indexMethod(index) {
            let curpage = this.pageNum   //
            let limitpage = this.pageSize   //
            return (index + 1) + (curpage - 1) * limitpage
        },
        //
        submit() {
            this.$refs.form.validate((valid) => {
                if (valid) {
                    this.request.post("/user", this.form).then(res => {
                        if (res.code === "200") {
                            this.$message.success("")
                            this.dialogVisible = false

                            this.load()
                        } else {

                            this.$message.error("")
                        }
                    })
                } else {
                    return false;
                }
            })
        },
        //
        submitUpdate() {
            this.$refs.form.validate((valid) => {
                if (valid) {
                    this.request.post("/user/update", this.form).then(res => {
                        if (res.code === "200") {
                            this.$message.success("")
                            this.updateDialogVisible = false
                            if (this.user.id === this.form.id) {

                                localStorage.setItem("user", JSON.stringify(this.form))
                            }
                            this.load()
                        } else {

                            this.$message.error("")
                        }
                    })
                } else {
                    return false;
                }
            })
        },
        //
        handleClose() {
            this.$refs.form.resetFields()
            this.dialogVisible = false
        },
        cancel() {
            this.handleClose()
        },
        updateCancel() {
            this.updateDialogVisible = false
        },
        handleEdit(row) {
            this.form.role
            this.modalType = 1
            // 
            this.form = JSON.parse(JSON.stringify(row))
            // this.dialogVisible = true
            this.updateDialogVisible = true

        },
        resetPassword(id) {
            this.$confirm(', ?', '', {
                confirmButtonText: '',
                cancelButtonText: '',
                type: 'warning'
            }).then(() => {
                this.request.post("/user/rePwd/" + id).then(res => {
                    if (res.code === '200') {
                        this.$message({
                            type: 'success',
                            message: '!'
                        });

                    }
                    // 
                    this.load()
                })

            }).catch(() => {
                this.$message({
                    type: 'info',
                    message: ''
                });
            });
        },
        handleDelete(id) {
            this.$confirm(', ?', '', {
                confirmButtonText: '',
                cancelButtonText: '',
                type: 'warning'
            }).then(() => {
                this.request.delete("/user/" + id).then(res => {
                    if (res.code === '200') {
                        this.$message({
                            type: 'success',
                            message: '!'
                        });
                    }
                    // 
                    this.load()
                })

            }).catch(() => {
                this.$message({
                    type: 'info',
                    message: ''
                });
            });
        },
        handleView(job_num) {
            this.request.get("/userInfo/" + job_num)
                .then(response => {
                    const userDetails = response.data; // 
                    this.viewUser = userDetails;
                    this.viewDialogVisible = true;
                    // console.log("userDetails:", userDetails);
                }).catch(error => {
                    console.error("", error);
                    // 
                });
        },
        handleCloseDetails() {
            this.viewUser = null;//viewUser
            this.dialogVisible = false;//
        },
        handleSelectionChange(val) {
            console.log(val)
            this.multipleSelection = val
        },
        handleBatchDelete() {
            if (this.multipleSelection.length === 0) {
                this.$message.warning("");
                return;
            }

            let ids = this.multipleSelection.map(v => v.id)  //[{},{}...]id[1.2.3]
            this.$confirm(', ?', '', {
                confirmButtonText: '',
                cancelButtonText: '',
                type: 'warning'
            }).then(() => {
                // this.axios.post("http://172.25.183.18:9090/user/del/batch", ids).then(res => {
                this.request.post("/user/del/batch", ids).then(res => {
                    if (res.code === '200') {
                        this.$message({
                            type: 'success',
                            message: '!'
                        });
                    }
                    // 
                    this.load()
                })

            }).catch(() => {
                this.$message({
                    type: 'info',
                    message: ''

                });
                this.load()
            });
        },
        //
        exp() {

            // window.location.href="http://192.168.1.14:9090/userInfo/export"
            window.location.href = "http://192.168.1.14:9090/userInfo/export"


        },

        handleAdd() {
            this.modelType = 0
            this.dialogVisible = true  //
            this.form = {}
        },
        load2() {
            this.pageNum = 1
            this.load()
        },
        load() {
            this.request.get("/user/page", {
                params: {
                    pageNum: this.pageNum,
                    pageSize: this.pageSize,
                    loginName: this.userForm.loginName,
                    id: this.userForm.id
                }
            }).then(res => {
                // this.tableData = res.data.records
                this.tableData = res.data.records.filter(item => item.role !== "");
                this.total = res.data.total

            })


        },
        handeleSizeChange(pageSize) {
            console.log("pageSize:" + pageSize)
            this.pageSize = pageSize
            this.load()
        },
        handleCurrentChange(pageNum) {
            this.pageNum = pageNum
            this.load()
        },
        // changeEnable(row) {
        //     console.log("1111")
        //     this.request.post("/user", row).then(res => {
        //         if (res.code === '200') {
        //             this.$message.success("")
        //         } else {
        //             this.$message.error("")
        //         }
        //     })
        // },
        loadRole() {
            this.request.get("/role")
                .then(res => {
                    console.log(res)
                    this.role = res.data
                    console.log(this.role)
                }
                )
        },
        change() {

        }
    },
}
</script>

