<template>
    <div class="manage">
        <!--  -->
        <el-dialog title="" :visible.sync="dialogVisible" width="50%" :before-close="handleClose">
            <!--  -->
            <el-form ref="formDialog" :rules="rules" :inline="true" :model="form" label-width="80px">
                <el-form-item label="" prop="id">
                    <el-input placeholder="" v-model="form.id"></el-input>
                </el-form-item>
                <el-form-item label="" prop="equipStatus">
                    <el-select v-model="form.equipStatus" placeholder="">
                        <el-option v-for="item in options" :key="item.value" :label="item.label" :value="item.value">
                        </el-option>
                    </el-select>
                </el-form-item>
                <el-form-item label="" prop="equipType">
                    <el-select v-model="form.equipType" placeholder="">
                        <el-option v-for="item in options2" :key="item.value" :label="item.label" :value="item.value">
                        </el-option>
                    </el-select>
                </el-form-item>
                <el-form-item label="" prop="equipName">
                    <el-input placeholder="" v-model="form.equipName"></el-input>
                </el-form-item>
                <el-form-item label="" prop="department">
                    <el-input placeholder="" v-model="form.department"></el-input>
                </el-form-item>
                <el-form-item label="">
                    <el-input placeholder="" v-model="form.price"></el-input>
                </el-form-item>
                <el-form-item label="">
                    <el-input placeholder="" v-model="form.model"></el-input>
                </el-form-item>
                <el-form-item label="" prop="manuFact">
                    <el-input placeholder="" v-model="form.manuFact"></el-input>
                </el-form-item>
                <el-form-item label="">
                    <el-date-picker v-model="form.acceptDate" type="date" placeholder=""  value-format="yyyy-MM-dd">
                    </el-date-picker>
                </el-form-item>
                <el-form-item label="" >
                    <el-date-picker v-model="form.prodDate" type="date" placeholder="" value-format="yyyy-MM-dd">
                    </el-date-picker>
                </el-form-item>
                <el-form-item label="">
                    <el-input placeholder="" v-model="form.facNum"></el-input>
                </el-form-item>
                <el-form-item label="">
                    <el-input placeholder="" v-model="form.purchCont"> </el-input>
                    <el-upload action="http://192.168.1.14:9090/fileEquipment/upload" :show-file-list="false"
                        :on-success="handleFileUploadSuccess" style="display: inline-block">
                        <el-button type="primary" size="mini" class="ml-5"></el-button>
                    </el-upload>
                </el-form-item>
                <el-form-item label="">
                    <el-input placeholder="" v-model="form.country"></el-input>
                </el-form-item>
                <el-form-item label="">
                    <el-input placeholder="" v-model="form.unit"></el-input>
                </el-form-item>
                <el-form-item label="">
                    <el-input placeholder="" v-model="form.num"></el-input>
                </el-form-item>
                <el-form-item label="">
                    <el-input placeholder="" v-model="form.equipFileNum"></el-input>
                </el-form-item>
                <el-form-item label="">
                    <el-input placeholder="" v-model="form.fileLoca"></el-input>
                </el-form-item>
                <!-- <el-form-item label="" prop="calibDate">
                    <el-date-picker v-model="form.calibDate" type="date" placeholder="">
                    </el-date-picker>
                </el-form-item> -->
                <el-form-item label="">
                    <el-input placeholder="" v-model="form.remarks"></el-input>
                </el-form-item>
            </el-form>
            <!--  -->
            <span slot="footer" class="dialog-footer">
                <el-button @click="cancel"> </el-button>
                <el-button type="primary" @click="submit"> </el-button>
            </span>
        </el-dialog>
        <!--  -->
        <el-form class="manage-header">
            <!--  -->
            <el-form-item>
                <el-button @click="handleAdd" type="primary"></el-button>
            </el-form-item>

            <el-form-item>
                <el-button @click="handleBatchDelete" type="danger" style="margin-left: 10px;"></el-button>
            </el-form-item>
            <el-form-item>
            <el-upload :action="exportUrl" :show-file-list="false" accept="xlsx" :on-success="handleExcelImportSuccess"
                    :on-error="handleExcelImportFail" style="display: inline-block;">
                    <el-button style="margin-left: 30px;" class="ml-5" type="primary"><i
                            class="el-icon-bottom"></i></el-button>
                </el-upload>
             </el-form-item>
              <el-form-item>
                <el-button @click="exp" style="margin-left: 30px;" type="primary"><i class="el-icon-top">
                    </i></el-button>
            </el-form-item>
            <el-form-item>
                <el-button @click="downloadTemplate" style="margin-left: 20px;" class="download-template-btn"
                    type="text"></el-button>
            </el-form-item>

            <el-form-item style="margin-left: 600px;">
                <el-input placeholder="" v-model="equipForm.equipName"></el-input>
            </el-form-item>
            <el-form-item>
                <el-button @click="load" type="primary" style="margin-left: 10px;"></el-button>
                <el-button type="warning" @click="reset"></el-button>
            </el-form-item>
        </el-form>
        <!--  -->
        <el-table  :data="tableData" style="width: 8.0rem" border stripe
            @selection-change="handleSelectionChange">
            <el-table-column type="selection" width="55" align="center"></el-table-column>
            <el-table-column type="index" label="" width="60" align="center" :index="indexMethod"></el-table-column>
            <el-table-column prop="equipStatus" label="" align="center">
                <template slot-scope="scope">
                    <el-tag type="success" v-if="scope.row.equipStatus === ''"></el-tag>
                    <el-tag type="warning" v-if="scope.row.equipStatus === ''"></el-tag>
                    <el-tag type="danger" v-if="scope.row.equipStatus === ''"></el-tag>
                </template>
            </el-table-column>
            <el-table-column prop="id" label="" align="center" width="150px" show-overflow-tooltip></el-table-column>
            <el-table-column prop="equipType" label="" align="center" width="150px" show-overflow-tooltip></el-table-column>
            <el-table-column prop="equipName" label="" align="center"></el-table-column>
            <el-table-column prop="department" label="" align="center"></el-table-column>
            <el-table-column prop="price" label="" align="center"></el-table-column>
            <el-table-column prop="model" label="" align="center"></el-table-column>
            <el-table-column prop="manuFact" label="" align="center"></el-table-column>
            <el-table-column prop="acceptDate" label="" align="center" width="100px"></el-table-column>
            <el-table-column prop="prodDate" label="" align="center" width="100px"></el-table-column>
            <el-table-column prop="facNum" label="" align="center" width="150px" show-overflow-tooltip></el-table-column>
            <el-table-column prop="purchCont" label="" align="center" width="160px" show-overflow-tooltip>
                <template slot-scope="scope">
                    <el-link type="primary" @click="watch(scope.row.purchContPath)">
                        {{ scope.row.purchCont }}
                    </el-link>
                </template>
            </el-table-column>
            <el-table-column prop="remarks" label="" align="center"></el-table-column>
            <el-table-column prop="country" label="" align="center"></el-table-column>
            <el-table-column prop="unit" label="" align="center"></el-table-column>
            <el-table-column prop="num" label="" align="center"></el-table-column>
            <el-table-column prop="equipFileNum" label="" align="center"></el-table-column>
            <el-table-column prop="fileLoca" label="" align="center"></el-table-column>
            <!-- <el-table-column prop="calibDate" label="" align="center"></el-table-column> -->
            <el-table-column label="" width="250" align="center" fixed="right">
                <template slot-scope="scope">
                    <el-button type="primary" size="mini" @click="handleDownload(scope.row.purchContPath)"></el-button>
                    <el-button size="mini" type="primary" @click="handleEdit(scope.row)"></el-button>
                    <el-button style="margin-left:5px" size="mini" type="danger" @click="handleDelete(scope.row.id)">
                    </el-button>

                </template>
            </el-table-column>
        </el-table>
        <!--  -->
        <div style="padding: 10px 0">
            <el-pagination @size-change="handleSizeChange" @current-change="handleCurrentChange" :current-page="pageNum"
                :page-sizes="[2, 5, 10, 20]" :page-size="pageSize" layout="total, sizes, prev, pager, next, jumper"
                :total="total">
            </el-pagination>
        </div>
    </div>
</template>

<script>
export default {
    data() {
        return {
            editedFormData: {},
            dialogVisible: false,
            tableData: [],
           
            modelType: 0,//01,
            total: 0,//
            pageNum: 1,
            pageSize: 10,
            equipForm: {
                equipName: '',
                // calibDate: '',
            },
            options: [{
                value: '',
                label: ''
            }, {
                value: '',
                label: ''
            }, {
                value: '',
                label: ''
            }],

            options2: [{
                value: '',
                label: ''
            }, {
                value: '',
                label: ''
            }, {
                value: '',
                label: ''
            }, {
                value: '',
                label: ''
            }, {
                value: '',
                label: ''
            },
            ],
            form: {
                purchCont: '',
                purchContPath: '',
                selection: '',
                index: '',
                equipStatus: '',
                id: '',
                originalId: '',
                equipType: '',
                equipName: '',
                department: '',
                price: '',
                model: '',
                manuFact: '',
                acceptDate: '',
                prodDate: '',
                remarks: '',
                country: '',
                unit: '',
                num: '',
                equipFileNum: '',
                fileLoca: '',
                calibDate: '',
            },
            rules: {
                purchCont: [
                    { required: true, message: "", trigger: "submit" }
                ],
                equipStatus: [
                    { required: true, message: "", trigger: "blur" }
                ],
                id: [
                    { required: true, message: "", trigger: "blur" }
                ],
                equipType: [
                    { required: true, message: "", trigger: "blur" }
                ],
                equipName: [
                    { required: true, message: "", trigger: "blur" }
                ],
                department: [
                    { required: true, message: "", trigger: "blur" }
                ],
                price: [
                    { required: true, message: "", trigger: "blur" }
                ],
                model: [
                    { required: true, message: "", trigger: "blur" }
                ],
                manuFact: [
                    { required: true, message: "", trigger: "blur" }
                ],
                acceptDate: [
                    { required: true, message: "", trigger: "blur" }
                ],
                prodDate: [
                    { required: true, message: "", trigger: "blur" }
                ],
                facNum: [
                    { required: true, message: "", trigger: "blur" }
                ],

                remarks: [
                    { required: true, message: "", trigger: "blur" }
                ],
                country: [
                    { required: true, message: "", trigger: "blur" }
                ],
                unit: [
                    { required: true, message: "", trigger: "blur" }
                ],
                num: [
                    { required: true, message: "", trigger: "blur" }
                ],
                equipFileNum: [
                    { required: true, message: "", trigger: "blur" }
                ],
                fileLoca: [
                    { required: true, message: "", trigger: "blur" }
                ],
                calibDate: [
                    { required: true, message: "", trigger: "blur" }
                ]
            }
        }
    },
    created() {
        this.load()
    },
    computed: {
        exportUrl() {
            return "http://192.168.1.14:9090/equipment/import"
        },
    },
    methods: {

        handleEdit(row) {
            console.log(row,row.id)
            this.originalId = row.id;
       
            this.modalType = 1
            // 
            this.form = JSON.parse(JSON.stringify(row))
            this.dialogVisible = true

        },
        //
        handleExcelImportSuccess() {
            this.$message.success("")
            this.load()
        },
        //
        handleExcelImportFail() {
            this.$message.error("")
            this.load()
        },
        // handleEdit(row) {
        //     // this.modelType = 1
        //     // 
        //     this.originalId = row.id;
        //     this.editedFormData = Object.assign({}, row);
        //     this.form = JSON.parse(JSON.stringify(row))
        //     this.dialogVisible = true

        // },
        handleDownload(path) {
            window.open(path + '#toolbar=0', '_blank')
        },
        // handleChange(file){

        //     this.form.purchCont = file.name
        //     this.$forceUpdate();
        //     // console.log( this.form.purchCont)
        // },
        handleFileUploadSuccess(res) {
            console.log(res)
            // this.form.purchCont=''
            if (res.code === "200") {
                this.form.purchCont = res.data.name
                this.form.purchContPath = res.data.path
                this.$forceUpdate();
                this.$message.success("")
                // console.log(  res.data.name)

                // this.refreshForm(); 
                // console.log(   this.form.purchCont)
            } else {
                this.$message.error("")
            }

            // this.load()
        },
        watch(path) {
            // window.open(path + '#toolbar=0', '_blank' )
            // let url = "http://127.0.0.1:8012/onlinePreview?url=" + encodeURIComponent(Base64.encodeURI(path));
            let url = "http://192.168.1.14:8012/onlinePreview?url=" + path;
            window.open(url);
        },
        indexMethod(index) {
            let curpage = this.pageNum   //
            let limitpage = this.pageSize   //
            return (index + 1) + (curpage - 1) * limitpage
        },
        exp() {
            // window.location.href = "http://192.168.1.14:9090/equipLedger/export";
            window.location.href = "http://192.168.1.14:9090/equipment/export" 
        },
        downloadTemplate() {
            // window.open("http://192.168.1.14:9090/file/210118f187944394ad5fc7f1cca2a028.xlsx")
            window.location.href = "http://192.168.1.14:9090/file/5cc40c484d544068a3ecfacc18328b80.xlsx"

        },
        //
        submit() {
           
            this.$refs.formDialog.validate((valid) => {

                if (valid) {
                    if(this.originalId === this.form.id){
                        this.request.put("/equipment",this.form).then(res => {
                            this.$message.success("");
                            this.dialogVisible = false;
                            this.load();
                        })
                    }else{
                        this.request.post("/equipment", this.form).then(res => {
                
                        if (res.code === "200") {
                            console.log("originalId", this.originalId )
                        // 
                        this.handleDeletee(this.originalId); 
                            this.$message.success("");
                            this.dialogVisible = false;
                            this.load();
                        }else if(res.code === "402"){
                            this.$message.error("")
                        }else {
                            this.$message.error("");
                        }
                    });
                    }
                    
                } else {
                    return false;
                }
        });
},

        handleClose() {
            this.$refs.formDialog.resetFields()
            this.dialogVisible = false
        },
        cancel() {
            this.handleClose()
        },
        //  //
        //  exp(){
        //     window.open("http://192.168.1.14:9090/equipLedger/export")
        //  },

        //  //
        //  handleExcelImportSuccess(){
        //     this.$message.success("")
        //     this.load()
        //  },
        //   //
        // handleExcelImportFail(){
        //     this.$message.error("")
        //     this.load()
        //  },

        //
        reset() {
            this.equipForm.name = ""
            this.load()
        },

        handleDelete(id) {
            this.$confirm(', ?', '', {
                confirmButtonText: '',
                cancelButtonText: '',
                type: 'warning'
            }).then(() => {
                this.request.delete("/equipment/" + id).then(res => {
                    if (res.code === '200') {
                        this.$message({
                            type: 'success',
                            message: '!'
                        });
                    }
                    // 
                    this.load()
                })

            }).catch(() => {
                this.$message({
                    type: 'info',
                    message: ''
                });
            });
        },
        handleDeletee(originalId) {
    this.request.delete("/equipment/" + originalId).then(res => {
        if (res.code === '200') {
          
        }
        // 
        this.load()
    }).catch(() => {
        this.$message({
            type: 'error',
            message: ''
        });
    });
},

        handleSelectionChange(val) {
            console.log(val)
            this.multipleSelection = val
        },
        handleBatchDelete() {
            let ids = this.multipleSelection.map(v => v.id)  //[{},{}...]id[1.2.3]
            this.$confirm(', ?', '', {
                confirmButtonText: '',
                cancelButtonText: '',
                type: 'warning'
            }).then(() => {
                this.request.post("/equipment/del/batch", ids).then(res => {
                    if (res.code === '200') {
                        this.$message({
                            type: 'success',
                            message: '!'
                        });
                    }
                    // 
                    this.load()
                })

            }).catch(() => {
                this.$message({
                    type: 'info',
                    message: ''

                });
                this.load()
            });
        },
        handleAdd() {
            this.modelType = 0
            this.dialogVisible = true  //
            this.form = {}
        },
        load() {
            this.request.get("/equipment/page", {
                params: {
                    pageNum: this.pageNum,
                    pageSize: this.pageSize,
                    name: this.equipForm.name,
                }
            })
                .then(
                    res => {
                        // console.log(res)
                        this.tableData = res.data.records
                        this.total = res.data.total
                    }
                )
        },
        handleCurrentChange(pageNum) {
            this.pageNum = pageNum
            this.load()
        },
        handleSizeChange(pageSize) {
            this.pageSize = pageSize
            this.load()
        },
        changeEnable(row) {
            this.request.post("/equipment", row).then(res => {
                if (res.data.code === '200') {
                    this.$message.success("")
                } else {
                    this.$message.error("")
                }
            })
        },

    },
};
</script>

<style lang="less" scoped>
.manage {
    height: 90%;

    .manage-header {
        display: flex;
        // justify-content: space-between;
        align-items: center;
    }

    .common-table {
        position: relative;
        height: calc(100% - 62px);

        .pager {
            // position: absolute;
            // bottom: 0;
            // right: 20px;
            padding: 10px 0;
        }
    }
}
</style>