                </el-form-item>
                <el-form-item label="" prop="manuFact">
                    <el-input placeholder="" v-model="form.manuFact"></el-input>
                </el-form-item>
                <el-form-item label="" prop="type">
                    <el-select placeholder="" v-model="form.type">
                        <el-option v-for="item in placeOptions" :key="item.value" :label="item.label"
                            :value="item.value"></el-option>
                    </el-select>
                </el-form-item>
                <el-form-item label="">
                    <el-input placeholder="" v-model="form.purchCont"> </el-input>
                    <el-upload action="http://192.168.1.14:9090/fileEquipment/upload" :show-file-list="false"
                        :on-success="handleFileUploadSuccess" style="display: inline-block">
                        <el-button type="primary" size="mini" class="ml-5"></el-button>
                    </el-upload>
                </el-form-item>
                <el-form-item label="">
                    <el-input placeholder="" v-model="form.remarks"></el-input>
                </el-form-item>
            </el-form>
            <!--  -->
            <span slot="footer" class="dialog-footer">
                <el-button type="success" @click="open"></el-button>
                <el-button @click="cancel"> </el-button>
                <el-button type="primary" @click="submit"> </el-button>
            </span>
        </el-dialog>
        <!--  -->
        <el-form class="manage-header">
            <!--  -->
            <el-form-item>
                <el-button @click="handleAdd" type="primary"></el-button>
            </el-form-item>
            <el-form-item>
                <el-button @click="handleBatchDelete" type="danger" style="margin-left: 3vh;"></el-button>
            </el-form-item>
            <el-form-item>
                <!-- <el-upload   action="http://192.168.1.14:9090/equipLedger/import" :show-file-list="false" accept="xlsx"  -->
                <el-upload :action="exportUrl" :show-file-list="false" accept="xlsx" :on-success="handleExcelImportSuccess"
                    :on-error="handleExcelImportFail" style="display: inline-block;">
                    <el-button style="margin-left: 30px;" class="ml-5" type="primary"><i
                            class="el-icon-bottom"></i></el-button>
                </el-upload></el-form-item> <el-form-item>
                <el-button @click="exp" style="margin-left: 30px;" type="primary"><i class="el-icon-top">
                    </i></el-button>
            </el-form-item>
            <el-form-item>
                <el-button @click="downloadTemplate" style="margin-left: 20px;" class="download-template-btn"
                    type="text"></el-button>
            </el-form-item>
            <el-form-item style="margin-left:20vh;">
                <el-input placeholder="" v-model="equipForm.name" style="width: 20vh;"></el-input>
            </el-form-item>

            <el-form-item>
                <el-input placeholder="" v-model="equipForm.model"
                    style="width:  20vh; margin-left: 20px;"></el-input>
            </el-form-item>
            <el-form-item>
                <el-button @click="load2" type="primary" style="margin-left: 20px;"></el-button></el-form-item>
            <el-form-item>
                <el-button type="warning" @click="reset" style="margin-left: 10px;"></el-button>
            </el-form-item>
        </el-form>
        <!--  -->
        <el-form inline style="width: 100%;">
            <el-form-item style="color: red; margin-top: 10px;  font-weight: bold; margin-left: 50px;">
                , {{ this.dayCount }} 30
            </el-form-item>
            <el-form-item style="margin-left:27vh;">
                <el-input placeholder="" v-model="equipForm.validity" style="width: 20vh;"></el-input>
            </el-form-item>

            <el-form-item style="margin-left: 20px;">
                <el-input placeholder="()" v-model="equipForm.cycle" style="width: 20vh;"></el-input>
            </el-form-item>
            <el-form-item style="margin-left: 20px;">
                <el-input placeholder="/" v-model="equipForm.verOrg" style="width: 20vh;"></el-input>
            </el-form-item>
            <el-form-item>
                <el-button @click="load2" type="primary" style="margin-left:15px;"></el-button>
                <el-button type="warning" @click="reset" style="margin-left: 18px;"></el-button>
            </el-form-item>
        </el-form>
        <!--  -->
        <!-- <el-color-picker v-model="selectedColor"></el-color-picker> -->
        <!--  -->
        <!-- <el-popover placement="left" trigger="hover" class="popover" :popper-options="popperOptions" @show="showPopover" > -->
        <el-popover placement="left" trigger="hover" class="popover" @show="showPopover">
            <div style="display: flex; justify-content: space-between;align-items: center; ">
                <el-checkbox v-model="checkAll" @change="checkAllChangeFn"></el-checkbox>
                <el-button type="text" @click="resetEnable(true)"></el-button>
            </div>
            <el-divider></el-divider>
            <el-checkbox-group v-model="checkColumns" @change="changeColumns">
                <el-checkbox v-for="(item, key) in columns" :key="key" :label="item.label"></el-checkbox>
            </el-checkbox-group>
            <i class="el-icon-s-tools" slot="reference" style="margin-right: 1px;width: 5px;"></i>
        </el-popover>

        <!--  -->
        <el-tabs v-model="activeName" type="card" @tab-click="handleClick" style="margin-top: -30px; width: 90%;">
            <el-tab-pane label="" name="first"></el-tab-pane>
            <el-tab-pane label="EMC" name="second"></el-tab-pane>
            <el-tab-pane label="" name="third"></el-tab-pane>
            <el-tab-pane label="" name="fourth"></el-tab-pane>
        </el-tabs>

        <!--  -->
        <el-table  :data="tableData" ref="filterTable" border @selection-change="handleSelectionChange"
            :row-class-name="tableRowClassName" @filter-change="handeleFilterChange">
            <el-table-column type="selection" width="55" align="center"></el-table-column>
            <el-table-column type="index" label="" width="60" align="center" :index="indexMethod"></el-table-column>
            <template v-for="item in columns">
                <el-table-column v-if="showColumn(item.prop)" :prop="item.prop" :key="item.label" :label="item.label"
                    align="center">
                </el-table-column>
            </template>
            <el-table-column prop="purchCont" label="" align="center" width="160px" show-overflow-tooltip>
                <template slot-scope="scope">
                    <el-link type="primary" @click="handleDownload(scope.row.purchContPath)">
                        {{ scope.row.purchCont }}
                    </el-link>
                </template>
            </el-table-column>
            <el-table-column prop="enable" label="" align="center">
                <template slot-scope="scope">
                    <el-switch v-model="scope.row.enabled" active-color="#13ce66" inactive-color="#ccc"
                        @change="changeEnable(scope.row)"></el-switch>
                </template>
            </el-table-column>
            <el-table-column label="" width="200" align="center">
                <template slot-scope="scope">
                    <el-button size="mini" type="primary" @click="handleEdit(scope.row)"></el-button>
                    <el-button style="margin-left:5px" size="mini" type="danger" @click="handleDelete(scope.row.id)">
                    </el-button>
                </template>
            </el-table-column>
        </el-table>
        <!--  -->
        <div style="padding: 10px 0">
            <el-pagination @size-change="handleSizeChange" @current-change="handleCurrentChange" :current-page="pageNum"
                :page-sizes="[2, 5, 10, 20]" :page-size="pageSize" layout="total, sizes, prev, pager, next, jumper"
                :total="total">
            </el-pagination>
        </div>
    </div>
</template>

<script>
export default {
    data() {
        return {
            dialogVisible: false,
            multipleSelection: [],
            activeName: 'first',
            tableData: [],
            validityFilters: [],
            verOrgFilters: [],
            cycleFilters: [],
            filtersForm: {
                validity: [],
                verOrg: [],
                cycle: []
            },
            options: [{
                value: '',
                label: ''
            }, {
                value: '',
                label: ''
            }, {
                value: '',
                label: ''
            }],
            placeOptions: [{
                value: 'first',
                label: ''
            }, {
                value: 'second',
                label: 'EMC'
            }, {
                value: 'third',
                label: ''
            }, {
                value: 'fourth',
                label: ''
            }],

            filteredData: [], // 
            modelType: 0,//01,
            total: 0,//
            pageNum: 1,
            pageSize: 10,
            equipForm: {
                id: '',
                selfNum: '',
                equipName: '',
                model: '',
                facNum: '',
                cerNum: '',
                validity: '',
                cycle: '',
                verOrg: '',
                remarks: '',
                measureRange: '',
                accuracy: '',
                usePlace: '',
                manuFact: ''
            },
            form: {
                id: '',
                selfNum: '',
                equipName: '',
                model: '',
                facNum: '',
                cerNum: '',
                validity: '',
                cycle: '',
                verOrg: '',
                remarks: '',
                measureRange: '',
                accuracy: '',
                usePlace: '',
                manuFact: '',
                equipStatus: '',
                purchCont: '',
                purchContPath: '',
                fixedNum: '',
            },
            // selectedColor: '', // 
            dayCount: 0,
            checkAll: false,
            checkColumns: [],
            columns: [],
            rules: {
                selfNum: [
                    { required: true, message: "", trigger: "blur" }
                ],
                equipName: [
                    { required: true, message: "", trigger: "blur" }
                ],
                model: [
                    { required: true, message: "", trigger: "blur" }
                ],
                facNum: [
                    { required: true, message: "", trigger: "blur" }
                ],
                cerNum: [
                    { required: true, message: "", trigger: "blur" }
                ],
                validity: [
                    { required: true, message: "", trigger: "blur" }
                ],
                cycle: [
                    { required: true, message: "", trigger: "blur" }
                ],
                verOrg: [
                    { required: true, message: "/", trigger: "blur" }
                ],
                measureRange: [
                    { required: true, message: "", trigger: "blur" }
                ],
                accuracy: [
                    { required: true, message: "", trigger: "blur" }
                ],
                usePlace: [
                    { required: true, message: "", trigger: "blur" }
                ],
                manuFact: [
                    { required: true, message: "", trigger: "blur" }
                ],
                type: [
                    { required: true, message: "", trigger: "blur" }
                ],
                equipStatus: [
                    { required: true, message: "", trigger: "blur" }
                ],

            }
        }
    },
    created() {
        this.load()
        this.getAll()
        this.getAllField()
        this.getDayCount()
    },
    computed: {
        exportUrl() {
            return "http://192.168.1.14:9090/equipLedger/import?type=" + this.activeName
        },
    },
    methods: {

        handleDownload(path) {
            window.location.href = path
        },
        getDayCount() {
            this.request.get("/equipLedger/expire").then(res => {
                // console.log("hideField")
                // console.log(res)
                this.dayCount = res.data;
            });
        },
        handleFileUploadSuccess(res) {
            console.log(res)
            // this.form.purchCont=''
            if (res.code === "200") {
                this.form.purchCont = res.data.name
                this.form.purchContPath = res.data.path
                this.$forceUpdate();
                this.$message.success("")
                // console.log(  res.data.name)

                // this.refreshForm(); 
                // console.log(   this.form.purchCont)
            } else {
                this.$message.error("")
            }

            // this.load()
        },
        //
        changeEnable(row) {

            this.request.post("/equipLedger", row).then(res => {
                // console.log("....")
                //  console.log(row)
                if (res.code === '200') {
                    this.$message.success("")
                } else {
                    this.$message.error("")
                }
            })
        },
        //
        getAllField() {
            this.request.get("/hideField").then(res => {
                console.log("hideField")
                console.log(res)
                this.columns = res.data;
            });
        },
        updateHideField() {
            this.request.post("/hideField/batchSave", this.columns).then(res => {
                if (res.code === "200") {
                    this.$message.success("")
                    // this.dialogVisible = false
                    // this.activeName = this.form.type
                    this.load()
                } else {
                    this.$message.error("")
                }
            })

        },
        // 
        checkAllChangeFn(val) {
            if (val) {
                // 
                this.resetEnable(true);
            } else {
                // 
                this.resetEnable(false);
            }
        },
        // 
        showColumn(currentColumn) {
            return this.columns.find(item => item.prop == currentColumn).isShow;
        },

        // showColumn(currentColumn) {
        //     return this.columns.filter(item => item.prop === "purchCont").find(item => item.prop === currentColumn).isShow;
        // },
        //flag: Booleanflag
        resetEnable(flag) {
            this.columns.forEach(item => {
                item.isShow = flag;
            })
            this.showPopover();
            this.refreshTable();
            //
            this.updateHideField();
        },

        /*  */
        changeColumns(val) {
            this.columns.forEach(item => {
                item.isShow = false;
            })
            // columnsvaltruefalse
            val?.forEach(item => {
                let current = this.columns.find(i => i.label == item)
                current.isShow = true;
            })
            // 
            this.judgeIsCheckAll();
            this.refreshTable();
            //
            this.updateHideField();
        },
        // 
        refreshTable() {
            this.$nextTick(() => {
                this.$refs.filterTable.doLayout();
            })
        },
        // 
        showPopover() {
            this.checkColumns = []
            this.columns.forEach(item => {
                if (item.isShow) {
                    this.checkColumns.push(item.label)
                }
            })
            // 
            this.judgeIsCheckAll();
        },
        // 
        judgeIsCheckAll() {
            //  =   
            if (this.checkColumns.length == this.columns.length)
                this.checkAll = true
            else
                this.checkAll = false
        },



        handleClick() {
            // console.log(this.activeName);
            this.pageNum = 1
            this.load()

        },
        downloadTemplate() {
            // window.open("http://192.168.1.14:9090/file/210118f187944394ad5fc7f1cca2a028.xlsx")
            window.location.href = "http://192.168.1.14:9090/file/210118f187944394ad5fc7f1cca2a028.xlsx"

        },
        //
        getAll() {
            this.request.get("/equipLedger").then(res => {
                const records = res.data;
                this.handleFilterField(records)

            });
        },
        handeleFilterChange(filter) {
            //
            this.pageNum = 1
            //
            if (filter.validity)
                this.filtersForm.validity = filter.validity.join(',')
            if (filter.cycle)
                this.filtersForm.cycle = filter.cycle.join(',')
            if (filter.verOrg)
                this.filtersForm.verOrg = filter.verOrg.join(',')

            this.request.get("/equipLedger/filter", {
                params: {
                    pageNum: this.pageNum,
                    pageSize: this.pageSize,
                    validity: this.filtersForm.validity,
                    verOrg: this.filtersForm.verOrg,
                    cycle: this.filtersForm.cycle
                }

            }).then(res => {
                this.tableData = res.data.records
                this.total = res.data.total
                this.handleFilterField(res.data.records)
            });
        },
        indexMethod(index) {
            let curpage = this.pageNum   //
            let limitpage = this.pageSize   //
            return (index + 1) + (curpage - 1) * limitpage
        },
        //
        submit() {
            this.$refs.form.validate((valid) => {
                if (valid) {
                    this.request.post("/equipLedger", this.form).then(res => {
                        if (res.code === "200") {
                            this.$message.success("")
                            this.dialogVisible = false
                            this.activeName = this.form.type
                            this.load()
                        } else {
                            this.$message.error("")
                        }
                    })
                } else {
                    return false;
                }
            })
        },
        handleClose() {
            this.$refs.form.resetFields()
            this.dialogVisible = false
        },
        cancel() {
            this.handleClose()
        },
        //
        exp() {
            // window.location.href = "http://192.168.1.14:9090/equipLedger/export";
            window.location.href = "http://192.168.1.14:9090/equipLedger/export/" + this.activeName;
        },


        //
        handleExcelImportSuccess() {
            this.$message.success("")
            this.load()
        },
        //
        handleExcelImportFail() {
            this.$message.error("")
            this.load()
        },
        //
        //
        reset() {
            this.equipForm.name = ""
            this.equipForm.model = ""
            this.equipForm.validity = ""
            this.equipForm.cycle = ""
            this.equipForm.verOrg = ""
            this.activeName = "first"

            this.filtersForm.validity = ""
            this.filtersForm.verOrg = ""
            this.filtersForm.cycle = ""
            this.$refs.filterTable.clearFilter()
            this.load()
        },
        handleEdit(row) {
            this.modelType = 1
            // 
            this.form = JSON.parse(JSON.stringify(row))
            this.dialogVisible = true

        },
        handleDelete(id) {
            this.$confirm(', ?', '', {
                confirmButtonText: '',
                cancelButtonText: '',
                type: 'warning'
            }).then(() => {
                this.request.delete("/equipLedger/" + id).then(res => {
                    if (res.code === '200') {
                        this.$message({
                            type: 'success',
                            message: '!'
                        });
                    }
                    // 
                    this.load()
                })

            }).catch(() => {
                this.$message({
                    type: 'info',
                    message: ''
                });
            });
        },
        handleSelectionChange(val) {

            this.multipleSelection = val
        },
        handleBatchDelete() {
            if (this.multipleSelection.length === 0) {
                this.$message.warning("");
                return;
            }

            let ids = this.multipleSelection.map(v => v.id)  //[{},{}...]id[1.2.3]
            this.$confirm(', ?', '', {
                confirmButtonText: '',
                cancelButtonText: '',
                type: 'warning'
            }).then(() => {
                this.request.post("/equipLedger/del/batch", ids).then(res => {
                    if (res.code === '200') {
                        this.$message({
                            type: 'success',
                            message: '!'
                        });
                    }
                    // 
                    this.load()
                })

            }).catch(() => {
                this.$message({
                    type: 'info',
                    message: ''

                });
                this.load()
            });
        },
        handleAdd() {
            this.modelType = 0
            this.dialogVisible = true  //
            this.form = {}
        },
        load2() {
            this.pageNum = 1
            this.load()
        },
        load() {
            this.request.get("/equipLedger/filter", {
                params: {
                    pageNum: this.pageNum,
                    pageSize: this.pageSize,
                    name: this.equipForm.name,
                    type: this.activeName,
                    model: this.equipForm.model,
                    validity: this.equipForm.validity,
                    verOrg: this.equipForm.verOrg,
                    cycle: this.equipForm.cycle
                    // validity: this.filtersForm.validity,
                    // verOrg: this.filtersForm.verOrg,
                    // cycle:this.filtersForm.cycle
                }
            }).then(res => {
                this.tableData = res.data.records
                this.total = res.data.total
                //
                this.getAll()

            }
            )
        },
        //
        handleFilterField(records) {
            // 
            // 
            const dates = records.map(record => record.validity);

            //  Set 
            const uniqueDates = [...new Set(dates)];
            // 
            uniqueDates.sort((a, b) => new Date(a) - new Date(b));
            //  this.tableData  this.validityFilters
            // this.tableData = records;
            this.validityFilters = uniqueDates.map(date => {
                return {
                    text: date,
                    value: date
                };
            });



            ///
            const verOrgs = records.map(record => record.verOrg)
            const uniqueVerOrgs = [...new Set(verOrgs)];
            this.verOrgFilters = uniqueVerOrgs.map(verOrg => {
                return {
                    text: verOrg,
                    value: verOrg
                };
            });
            //
            const cycles = records.map(record => record.cycle)
            const uniqueCycles = [...new Set(cycles)];
            this.cycleFilters = uniqueCycles.map(cycle => {
                return {
                    text: cycle,
                    value: cycle
                };
            });
        },
        handleCurrentChange(pageNum) {
            this.pageNum = pageNum
            this.load()
        },
        handleSizeChange(pageSize) {

            this.pageSize = pageSize
            this.load()
        },
        // changeEnable(row) {
        //     this.request.post("/equipLedger", row).then(res => {
        //         if (res.data.code === '200') {
        //             this.$message.success("")
        //         } else {
        //             this.$message.error("")
        //         }
        //     })
        // },
        tableRowClassName({ row, rowIndex }) {
            const today = new Date();
            const validityDate = new Date(row.validity);
            //+
            validityDate.setMonth(validityDate.getMonth() + row.cycle);

            // 
            const daysRemaining = Math.floor((validityDate - today) / (1000 * 60 * 60 * 24) + 1);


            // 30
            if (0 <= daysRemaining && daysRemaining <= 30) {

                // return this.selectedColor; // 
                return { 'highlight-row': true, 'top': '0' }; // 
            }

            if (daysRemaining < 0) {
                return { 'gray-row': true, 'bottom': '0' }; // 
            }

            return '';// 
        },
        // open() {
        //     this.$confirm('?', '', {
        //         confirmButtonText: '',
        //         cancelButtonText: '',
        //         type: 'warning'
        //     }).then(() => {
        //         this.$refs.form.validate((valid) => {
        //             if (valid) {
        //                 Promise.all([
        //                     this.request.post("/equipLedger", this.form),
        //                     this.request.post("/equipment", this.form)
        //                 ]).then(responses => {
        //                     const equipLedgerRes = responses[0];
        //                     const equipmentRes = responses[1];
