        open() {
            this.$confirm('?', '', {
                confirmButtonText: '',
                cancelButtonText: '',
                type: 'warning'
            }).then(() => {
                this.$refs.form.validate((valid) => {
                    if (valid) {
                        this.request.post("/equipLedger/uuid", this.form)
                            .then(responses => {
                                const equipLedgerRes = responses;
                                if (equipLedgerRes.code === "200") {
                                    // 
                                    this.$message.success("");
                                    this.dialogVisible = false;
                                    // 
                                    this.load();
                                } else {
                                    this.$message.error("");
                                }
                            });
                    } else {
                        return false;
                    }
                });
            }).catch(() => {
                this.$message({
                    type: 'info',
                    message: ''
                });
            });
        }
    },
};
</script>

